@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.form {

  &__groups {
    display: flex;
    flex-wrap: wrap;
    margin: -20px 0 0 -30px;
  }

  &__group {
    flex-basis: 100%;
    margin: 20px 0 0 30px;
  }

  &__group-row {
    display: flex;
    flex-wrap: wrap;
    margin: -10px 0 0 0;
  }

  &__group-item {
    flex-basis: 100%;
    margin: 10px 0 0 0;
  }

  &__input {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0 0 10px;
    border: solid #8C8C8C;
    border-width: 0 0 1px;
    background-color: transparent;
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 18px;
    line-height: 145%;
    color: #F5F5F5;
    outline: none;
    transition: $transition_main;

    &:focus,
    &:hover {
      border-color: #F5F5F5;
    }

    &::placeholder {
      font-family: "IBM Plex Mono";
      font-weight: 400;
      font-size: 18px;
      line-height: 145%;
      letter-spacing: .01em;

      color: #BFBFBF;
    }

    &.has-error {
      border-color: #D95B4F;
    }
  }

  &__footer {
    margin-top: 30px;
  }

  &__notice {
    margin: 20px -20px 0;
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;   
    text-align: center;
    color: #595959;
    transition: $transition_main;
  }

  &__link {
    color: #F08F3B;
    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .64;
    }
  }

  &__additional {
    display: flex;
    align-items: center;
    text-decoration: none;
    transition: 0.3s ease-out;

    transition: $transition_main;

    &:hover,
    &:active {
      opacity: 0.6;
    }
  }

  &__additional-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    margin: 0 10px 0 0;
    border-radius: 50%;
    overflow: hidden;
    background-color: #F08F3B;
  }

  &__additional-icon  .icon-download-file {
    width: 16px;
    height: 16px;
  }

  &__additional-title {
    font-family: 'IBM Plex Mono';
    font-weight: 600;
    font-size: 16px;
    line-height: 21px;

    color: #262626;
  }
}

.form_theme_dark {

  .form {

    &__input {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0 0 10px;
      border: solid #8C8C8C;
      border-width: 0 0 1px;
      background-color: transparent;
      font-family: "IBM Plex Mono";
      font-weight: 400;
      font-size: 18px;
      line-height: 145%;
      color: #262626;
      outline: none;
      transition: $transition_main;
  
      &:focus,
      &:hover,
      &.filled {
        border-color: #262626;
      }
  
      &::placeholder {
        font-family: "IBM Plex Mono";
        font-weight: 400;
        font-size: 18px;
        line-height: 145%;
        letter-spacing: .01em;
  
        color: #BFBFBF;
      }

      &.has-error {
        border-color: #D95B4F;
      }
    }
  }
}

.has-error {

  .form__input {
    border-color: #D95B4F;
  }
}
