@keyframes fadeIn {

  0% {
    display: block;
    opacity: 0;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

@keyframes fadeOut {

  0% {
    display: block;
    opacity: 0;
  }

  100% {
    display: none;
    opacity: 0;
  }
}

@keyframes headerMenuOverlayTransitionOut {

  0% {
    opacity: 1;
    background-color: #32898D;
    transform: translateY(0);
  }

  25% {
    transform: translateY(-20px);
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    background-color: #fff;
    transform: translateY(-12px);
  }
}

@keyframes headerFadeTransitionIn {

  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes headerTopTransitionIn {

  0% {
    opacity: 0;
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes headerTopTransitionOut {

