@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

$scrollStepValue: 30vh;

[data-anim-entity="main-screen"] {
  opacity: 0;
  transform: translateY($scrollStepValue);
  will-change: opacity, transform;
  padding-top: calc(($scrollStepValue * 2) + 98px);

  @include media(from_l) {
    padding-top: calc(($scrollStepValue * 2) + 98px);
  }
}

[data-anim-entity="screens-container"] {
  width: 100%;
  overflow: hidden;
}

[data-anim-entity="intro-screen"],
[data-anim-entity="picture-screen"] {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  will-change: opacity, transform;
}

[data-anim-entity="picture-screen"] {
  transform: translateY(100%);
  opacity: 0;
}

[data-anim-trigger="intro-screen"],
[data-anim-trigger="picture-screen"] {
  position: fixed;
  top: 0;
  left: 0;
  height: $scrollStepValue;
  width: 100%;
  pointer-events: none;
}

[data-anim-trigger="picture-screen"] {
  top: $scrollStepValue;
}
