@import "@theme/vars.scss";
@import "@theme-mixins/media.scss";

.c-vacancy {
  font-family: "IBM Plex Mono";
  border-top: 1px solid #8C8C8C;
  transition: background-color 0.35s ease;

  &:last-child{
    border-bottom: 1px solid #8C8C8C;
  }

  &:hover,
  &.is-open {
    border-color: #D95B4F;
  }

  &:hover &__header,
  &.is-open &__header {
    padding-right: 32px;
    padding-left: 20px;
    background-color: #D95B4F;
    color: #fff;
    fill: #fff;
  }

  &:hover &__icon {
    right: 20px;
  }
  

  &:hover &__subtitle,
  &.is-open &__subtitle {
    color: #fff;
  }
  
  &.is-open &__icon {
    right: 20px;
    transform: translateY(-50%) rotate(90deg);
  }

  &__header {
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 32px;
    min-height: 108px;
    padding: 20px 12px 20px 0;
    color: #262626;
    fill: #262626;
    transition: color 0.35s ease 0.15s, fill 0.35s ease 0.15s, background-color 0.35s ease, padding 0.45s cubic-bezier(.65,.05,.41,.95);
  }

  &__title {
    font-family: 'IBM Plex Mono';
    font-weight: 700;
    font-size: 26px;
    line-height: 110%;
    flex: 2 1 65%;
    padding-right: 40px;
    
    &:nth-last-child(1){
      padding-right: 20px;
    }
  }

  &__subtitle {
    font-family: 'IBM Plex Mono';
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    flex: 1 1 35%;
    padding-right: 20px;
    color: #595959;
    transition: color 0.35s ease 0.15s;
  }

  &__icon{
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    position: absolute;
    top: 50%;
    right: 0;
    transform-origin: center;
    transform: translateY(-50%);
    transition: right 0.45s cubic-bezier(.65,.05,.41,.95), transform 0.25s ease;
  }

  &__body {
    max-height: 0px;
    overflow: hidden;
    transition: max-height 0.5s cubic-bezier(.65,.05,.41,.95);
  }

  &__body-inner {
    box-sizing: border-box;
    padding: 40px 20px;
  }

  &__description {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px -24px;
  }

  &__item {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 0 10px;
    margin-bottom: 24px;
    flex: 0 1 50%;

    &>*{
      &:last-child{
        margin-bottom: 0;
      }
    }

    h4{
      font-family: 'Ermilov';
      font-weight: 700;
      font-size: 28px;
      line-height: 110%;
      color: #262626;
      margin: 0 0 20px;
    }

    p{
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.01em;
      color: #262626;
      margin: 0 0 10px;

      &:last-child{
        margin-bottom: 0;
      }
    }

    ul {
      display: flex;
      flex-direction: column;
      padding: 0;
      margin: 0;
      margin-bottom: 15px;
  
      li {
        position: relative;
        display: flex;
        flex-direction: column;
        color: #000;
        padding: 0 0 0 28px;

        &:last-child{
          margin-bottom: 0;
        }

        &::before{
          content: "";
          position: absolute;
          display: flex;
          flex-shrink: 0;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: #262626;
          top: 10px;
          left: 12px;
        }

        p{
          margin-bottom: 0;
        }
      }
    }
  }

  &__form {
    margin-top: 40px;
  }

  &__button{
    min-width: 224px;
    width: auto;
    margin-top: 40px;
    margin: 40px auto 0;
  }
}