@import "@theme/vars.scss";
@import "@theme-mixins/media.scss";

.m-vacancies {
  display: flex;
  flex-direction: column;
  color: #000000;
  background-color: #fff;
  padding: 120px 0;
  
  &__header {
    display: block;
    margin-bottom: 40px;
    max-width: 770px;

    &>*:last-child,
    &:last-child {
      margin-bottom: 0;
    }

    h2 {
      font-family: 'Ermilov';
      font-weight: 700;
      font-size: 45px;
      line-height: 110%;
      text-transform: uppercase;
      margin: 0 0 20px;
    }

    p{
      font-family: 'IBM Plex Mono';
      font-size: 16px;
      line-height: 140%;
      letter-spacing: 0.01em;
      padding: 0;
      margin: 0 0 12px;

      span{
        font-weight: 500;
      }
    }

    ul {
      display: flex;
      flex-direction: column;
      padding: 0;
      margin: 0;
      margin: 20px 0;

      li {
        position: relative;
        display: flex;
        flex-direction: column;
        color: #000;
        padding: 0 0 0 28px;

        &:last-child{
          margin-bottom: 0;
        }

        &::before{
          content: "";
          position: absolute;
          display: flex;
          flex-shrink: 0;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background-color: #262626;
          top: 10px;
          left: 12px;
        }

        p{
          margin-bottom: 5px;

          &:last-child{
            margin-bottom: 0;
          }
        }
      }
    }
  }


  &__inner {
  }

  &__list {
    display: flex;
    flex-direction: column;
  }

  &__item {

  }
}