@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.m-page {
  overflow-x: hidden;
  background-color: #F9F9F9;
}

.m-subtitle {
  margin: 0;
  font-family: "Ermilov";
  font-weight: 700;
  font-size: 35px;
  line-height: 110%;
  color: #262626;

  @include media(from_l) {
    font-size: 50px;
  }
}

.m-subtitle_theme_light {
  color: #fff;
}

.m-orange {
  color: $orange;
}

.m-red {
  color: $red;
}

.m-white {
  color: #fff;
}

.m-text {
  margin: 0;
  font-family: "IBM Plex Mono";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}

.m-text_theme_light {
  color: #fff;
}

.m-intro {
  position: relative;
  z-index: 1;
  box-sizing: border-box;
  overflow: hidden;
  padding: 140px 0 180px;
  background-color: #C34F44;

  @include media(from_l) {
    padding: 170px 0 270px;
  }

  &__title {
    color: #fff;
    max-width: 362px;
    margin: 0 0 30px;

    @include media(from_l) {
      max-width: 512px;
    }
  }

  &__row-wrapper {
    overflow: hidden;
  }

  &__row {
    display: flex;
    align-items: center;
    padding-left: 2px;
  }

  &__text {
    margin: 0;
    font-family: "Ermilov";
    font-weight: 700;
    font-size: 45px;
    line-height: 110%;
    letter-spacing: .01em;
    color: #fff;

    @include media(from_l) {
      font-size: 54px;
    }
  }

  &__text_stroked {
    color: transparent;
    text-shadow: none;
    -webkit-text-stroke: 1px #fff;
  }

  &__text_has-dash {
    padding: 0 4px;
  }

  &__pic {
    flex-shrink: 0;
    width: 135px;
    height: 66px;
    margin: 0 10px 0 0;
    border-radius: 5px;
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @include media(from_l) {
      margin: 0 16px 0 0;
      border-radius: 8px;
    }
  }

  &__button {
    .button{
      padding: 15px 30px;
      border-radius: 10px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    width: 100%;
    height: 100%;
    background-color: $red;
    transform: translateY(100%);
  }

  .button {
    padding: 15px;
    border-radius: 10px;
    border: 1px solid #000;
    font-size: 18px;
  }

  .button-container:hover .button {
    background-color: #000;
    color: #fff;
  }



  &__inner {
    box-sizing: border-box;
    width: 100%;
    margin: 0 auto;
    max-width: 900px;
  }

  &__bg {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__subtitle {
    position: relative;
    z-index: 2;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 20px;
    line-height: 145%;
    margin-bottom: 30px;
    color: #fff;

    @include media(from_l) {
      margin-bottom: 60px;
      font-size: 22px;
    }
  }

  &__logo {
    display: flex;
    width: 100%;
    height: 196px;

    @include media(from_l) {
      height: 236px;
    }

    div{
      image-rendering: -webkit-optimize-contrast;
      display: block;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.m-browser {
  position: relative;
  z-index: 2;
  
  &__inner {
    position: relative;
    z-index: 2;
    width: 100%;
    min-height: 600px;
    max-width: 900px;
    width: 100%;
    border-radius: 20px;
    background-color: #333333;
    overflow: hidden;
    margin: -210px auto 0;

    @include media(from_l) {
      margin-top: -300px;
    }
  }

  &__header {
    position: relative;
    z-index: 1;
    display: block;
    width: 100%;
    height: auto;
    object-fit: contain;
    image-rendering: -webkit-optimize-contrast;
  }

  &__main {
    position: relative;
    z-index: 1;
    margin: 65px 50px;
  }

  &__canvas-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    width: 100%;
    width: calc(100% - 120px);
    zoom: 0.8;
      
    @media only screen and (min-width: 1280px){
      zoom: 1;
    }
  }

  &__canvas {
    width: 100%;
    height: 100%;
  }
}

.m-projects {
  position: relative;
  z-index: 1;

  &__inner {
    position: relative;
    padding: 70px 0;

    @include media(from_l) {
      padding: 120px 0;
    }
  }

  &__header-wrapper {
    position: relative;
    margin: 0 0 60px;

    @include media(from_l) {
      margin: 0;
    }
  }

  &__header-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $red;
    background-image: url("../../../assets/media/m-projects-header.svg");
    background-repeat: no-repeat;
    background-size: 247px 212px;
    background-position: right -74px top 50%;

    @include media(from_l) {
      background-size: 368px 314px;
    }
  }

  &__header {
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
  }

  &__header-container {
    width: 100%;
    max-width: 50%;

    @include media(from_l) {
      max-width: 40%;
    }
  }

  .m-subtitle {
    margin-bottom: 30px;

    @include media(from_l) {
      max-width: 390px;
    }
  }

  &__list-wrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-top: 50px;

    @include media(from_l) {
      margin-top: 75px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    margin: -60px -30px 0;

    @include media(from_l) {
      margin: -100px -45px 0;
    }
  }

  &__item {
    position: relative;
    z-index: 20;
    margin: 60px 30px 0;
    flex-basis: calc(43% - 60px);
    height: 300px;
    
    @include media(from_l) {
      height: 372px;
      flex-basis: calc(43% - 90px);
      margin: 100px 45px 0;
    }

    &:nth-child(n) {
      height: 300px;
            
      @include media(from_l) {
        height: 372px;
      }
    }

    &:nth-child(1) {
      height: 540px;
      margin-bottom: 60px;
      flex-basis: calc(50% - 60px);

      @include media(from_l) {
        flex-basis: calc(43% - 90px);
        margin-bottom: 100px;
        height: 780px;
      }
    }

    &:nth-child(2),
    &:nth-child(3) {
      width: calc(50% - 60px);
      position: absolute;
      left: calc(50% + 20px);
      margin: 0;
      height: 320px;
      
      @include media(from_l) {
        height: 490px;
        left: calc(43% + 70px);
        max-width: 472px;
      }
    }

    &:nth-child(2) {
      top: -80px;
      
      @include media(from_l) {
        top: -220px;
      }
    }

    &:nth-child(3) {
      top: 280px;
      
      @include media(from_l) {
        top: 400px;
      }
    }

    &:nth-child(4) {
      flex-basis: 100%;
      height: 290px;

      @include media(from_l) {
        height: 384px;
      }
    }


    &:nth-child(4n - 3):not(:nth-child(1)),
    &:nth-child(4n):not(:nth-child(4)) {
      flex-basis: calc(43% - 60px);

      @include media(from_l) {
        flex-basis: calc(43% - 90px);
      }
    }

    &:nth-child(4n - 2):not(:nth-child(2)),
    &:nth-child(4n - 1):not(:nth-child(3)) {
      flex-basis: calc(57% - 60px);

      @include media(from_l) {
        flex-basis: calc(57% - 90px);
      }
    }

    &:last-child {
      height: 365px;
      flex-grow: 1;
      max-width: 70%;

      @include media(from_l) {
        max-width: 60%;
      }
    }


    &:last-child .card-project__pic-wrapper {
      background-color: #fff;
    }

    &:last-child:hover .card-project__pic-wrapper {
      transform: scale(1.1);
    }
  }


  &__item-link {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;

    @include media(from_l) {
      top: -50px;
      left: -50px;
      width: calc(100% + 100px);
      height: calc(100% + 100px);
      cursor: none;
    }
  }

  &__item-link:hover + .card-project {
    transform: scale(1.1);
  }
}

.m-services {

  &__inner {
    box-sizing: border-box;
    padding: 100px 0;
    background-color: #F0F0F0;

    @include media(from_l) {
      padding: 120px 0 60px;
    }
  }

  &__header {
    position: relative;
    max-width: 400px;
    margin: 0 0 60px;

    @include media(from_l) {
      max-width: 620px;
      margin: 0 0 70px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 100%;
      transform: translate(34px, -42px);
      width: 133px;
      height: 113px;

      background-repeat: no-repeat;
      background-size: contain;
      background-image: url("../../../assets/media/m-services-bg.svg");

      @include media(from_l) {
        width: 174px;
        height: 148px;
        transform: translate(42px, -48px);
      }
    }
  }

  .m-subtitle {
    position: relative;
    z-index: 1;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -20px 0 0;

    @include media(from_l) {
      margin: -12px 0 0 -12px;
    }
  }

  &__item {
    flex-basis: 100%;
    margin: 20px 0 0;
    overflow: hidden;

    @include media(from_l) {
      flex-basis: calc(50% - 12px);
      margin: 12px 0 0 12px;
    }
  }

  &__item:nth-child(3) {
    @include media(from_l) {
      flex-basis: calc(100%);
    }
  }

  &__item:nth-child(1) .card-service__icon {
    background-color: #C34F44;
  }

  &__item:nth-child(1) .card-service__pic-wrapper {
    @include media(from_l) {
      bottom: -12px;
      left: calc($cardServiceContentWidth_desktop + 80px);
      width: 406px;
      height: 320px;
    }
  }

  &__item:nth-child(2) .card-service__icon {
    background-color: #32898D;
  }

  &__item:nth-child(2) .card-service__pic-wrapper {
    @include media(from_l) {
      bottom: unset;
      top: -12px;
      left: calc($cardServiceContentWidth_desktop + 80px);;
      width: 400px;
      height: 310px;
    }
  }

  &__item:nth-child(3) .card-service__icon {
    background-color: #F08F3B;
  }

  &__item:nth-child(3) .card-service__pic-wrapper {
    @include media(from_l) {
      bottom: -8px;
      left: calc($cardServiceContentWidth_desktop + 144px);
      width: 630px;
      height: 290px;
    }
  }
}

.m-prices {

  &__inner {
    box-sizing: border-box;
    padding: 50px 0 100px;
    background-color: #F0F0F0;
  }

  &__header {
    margin: 0 0 60px;

    @include media(from_l) {
      margin: 0 0 70px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -23px 0 0;

    @include media(from_l) {
      margin: -20px 0 0;
    }
  }

  &__item {
    flex-basis: 100%;
    margin: 23px 0 0;

    @include media(from_l) {
      margin: 20px 0 0;
    }
  }
}

.m-benefits {
  position: relative;
  background-color: #1E475C;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
  }

  &__pic {
    position: absolute;
    top: -40px;
    right: calc((100% - $mainContentWidth_pad) / 2 - 30px);
    width: 388px;
    height: 164px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;

    @include media(from_l) {
      right: calc((100% - $mainContentWidth_desktop) / 2 - 30px);
    }
  }

  &__inner {
    position: relative;
    z-index: 2;
    padding: 100px 0;
    overflow: hidden;

    @include media(from_l) {
      padding: 120px 0;
    }
  }

  &__header {
    margin: 0 0 30px;

    @include media(from_l) {
      margin: 0 0 60px;
    }
  }

  &__desc {
    margin: 0 0 60px;
    font-family: "IBM Plex Mono";
    font-weight: 500;
    font-size: 22px;
    line-height: 145%;
    color: #FFFFFF;

    @include media(from_l) {
      font-size: 25px;
    }
  }

  &__desc p {
    margin: 0;
  }

  &__button {
    display: block;
    width: 100px;
    height: 100px;
    border: none;
    padding: 0;
    border-radius: 50%;
    background-color: $orange;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M50 70L55.7735 60H44.2265L50 70ZM49 30L49 61H51L51 30H49Z' fill='white'/%3E%3C/svg%3E%0A");
    cursor: pointer;
    transition: $transition_main;
  }

  .button-container:hover &__button,
  .button-container:active &__button  {
    background-color: #000;
  }
}

.m-bitrix {

  &__inner {
    padding: 60px 0 100px;
    background-color: #fff;

    @include media(from_l) {
      padding: 60px 0 120px;
    }
  }

  &__header {
    margin: 0 0 40px;

    @include media(from_l) {
      margin: 0 0 75px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -25px 0 0;
  }

  &__item {
    flex-basis: 100%;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(29, 70, 90, .18);
    margin: 25px 0 0;
    padding: 0 0 25px;

    @include media(from_l) {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }

    &:last-child {
      border: none;
      padding: 0;
    }
  }

  &__item-title {
    margin: 0 0 15px;
    font-family: "IBM Plex Mono";
    font-weight: 700;
    font-size: 22px;
    line-height: 29px;
    color: #262626;

    @include media(from_l) {
      flex-basis: 420px;
    }
  }

  &__item-desc {
    margin: 0;
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 16px;
    line-height: 145%;
    color: #595959;

    @include media(from_l) {
      flex-basis: 562px;
    }
  }
}

.m-features {
  
  &__inner {
    padding: 100px 0;
    background-color: $red;
  }

  &__header {
    margin: 0 0 20px;
  }

  &__desc {
    max-width: 695px;
    margin: 0 0 40px;
  }

  &__row {
    margin: 0 0 60px;

    @include media(from_l) {
      display: flex;
      margin: 0 0 40px;
    }
  }

  &__list {
    display: flex;
    margin: 0 0 0 -50px;
  }

  &__item {
    margin: 0 0 0 50px;
    max-width: 170px;
  }

  &__number {
    width: 100%;
    height: 48px;
    margin: 0 0 15px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 0 center;
  }

  &__number-desc {
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    letter-spacing: .01em;
    color: #FFFFFF;
  }

  &__slider-capture {
    margin: 0 0 30px;
    font-family: "Ermilov";
    font-weight: 700;
    font-size: 25px;
    line-height: 110.6%;
    color: #FFFFFF;

    @include media(from_l) {
      margin: 0 0 25px;
      font-size: 35px;
    }
  }

  .swiper-slide {
    width: 296px;
  }

  [data-entity="slider-shadow-left"] {
    background: linear-gradient(-270deg, #D95B4F -125.44%, rgba(217, 91, 79, 0) 84.62%);
  }

  [data-entity="slider-shadow-right"] {
    background: linear-gradient(270deg, #D95B4F -125.44%, rgba(217, 91, 79, 0) 84.62%);
  }
}

.m-clients {
  &__main {
    box-sizing: border-box;
    padding: 92px 0 100px;
    background-color: #F0F0F0;

    @include media(from_l) {
      position: relative;
      padding: 120px 0;

      &::before {
        content: "";
        position: absolute;
        bottom: 10px;
        right: 40px;
        width: 122px;
        height: 104px;
        opacity: .5;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("../../../assets/media/m-services-bg.svg");
      }
    }

    .swiper-slide {
      width: 495px;

      @include media(from_l) {
        width: 600px;
      }
    }
  }

  &__header {
    position: relative;
    margin: 0 0 60px;

    @include media(from_l) {
      margin: 0 0 70px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 100%;
      transform: translate(34px, -42px);
      width: 133px;
      height: 113px;

      background-repeat: no-repeat;
      background-size: contain;
      background-image: url("../../../assets/media/m-services-bg.svg");

      @include media(from_l) {
        width: 174px;
        height: 148px;
        transform: translate(42px, -48px);
      }
    }
  }

  .m-subtitle {
    position: relative;
    z-index: 1;
  }

  &__footer {
    height: 320px;
    box-sizing: border-box;
    padding: 90px 0;
    background-color: #F5F5F5;
  }

  &__logos {
    display: flex;
    flex-shrink: 0;
  }

  &__logo {
    height: 100%;
    width: 200px;
    margin: 0 120px 0 0;
  }

  [data-entity="slider-shadow-left"] {
    background: linear-gradient(-270deg, #F0F0F0 0%, rgba(240, 240, 240, 0) 84.62%);
  }

  [data-entity="slider-shadow-right"] {
    background: linear-gradient(270deg, #F0F0F0 0%, rgba(240, 240, 240, 0) 84.62%);
  }
}

.m-marquee {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 20px 0;
  background-color: #D95B4F;

  &__text {
    margin-right: 40px;
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 32px;
    text-transform: uppercase;
    color: #fff;
    
    white-space: nowrap;
  }
}
