@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

[data-anim-entity="projects-background"] {
  opacity: 0;
}

[data-anim-entity="projects-title"] {
  opacity: 0;
}

[data-anim-entity="project"] {
  height: 100%;
  width: 100%;
  overflow: hidden;
  opacity: 0;
  pointer-events: none;
}

[data-anim-entity="project"] {
  transform: scale(1.1);
}

[data-anim-entity="benefits-bitrix"] {
  overflow: hidden;
  background-color: #fff;
}

[data-anim-entity="marquee"] {
  transition: $transition_main;
  pointer-events: none;
}

[data-anim-entity="marquee"].hidden {
  opacity: 0;
  transform: translateY(100%);
}

// SECTION Z-INDEXES
[data-anim-entity="services"],
[data-anim-entity="prices"],
[data-anim-entity="benefits"],
[data-anim-entity="benefits-bitrix"],
[data-anim-entity="benefits-features"],
[data-anim-entity="clients"] {
  position: relative;
  z-index: 1;
  overflow: hidden;
}

[data-anim-entity="prices"] {
  z-index: 4;
}

[data-anim-entity="benefits"] {
  z-index: 3;
}

[data-anim-entity="benefits-bitrix"] {
  z-index: 2;
}
