@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.fds-screen-intro {
  background-color: #003399;

  .screen-intro {

    &__bg {
      background-color: #003399;
    }

    &__content {
      @include media(from_l) {
        padding-top: 320px;
      }    
    }

    &__header {
      margin: 0 0 77px auto;

      @include media(from_l) {
        position: relative;
        flex-direction: column;
        justify-content: flex-start;
        padding: 0;
        margin: 0;
        transform: translateY(100%);
        height: 0;
      }
    }

    &__info {
      @include media(from_l) {
        flex-basis: calc(25% + 40px);
        padding-left: 50%;
      }
    }


    &__title {
      margin-bottom: 77px;
      margin-left: auto;
      color: #FFFFFF;

      @include media(from_l) {
        align-items: flex-start;
        order: unset;
        margin-bottom: 65px;
        margin-left: calc(25% + 72px);
        transform: translateX(0);
      }
    }


    &__title-part_type_stroke {
      -webkit-text-stroke: 2px #B84236;
      transform: translateX(80px);

      @include media(from_l) {
        transform: translateX(306px);
      }
    }

    &__thumb {
      @include media(from_l) {
        margin-left: 0;
      }
    }

    &__button-container {
      @include media(from_l) {
        bottom: 200px;
        right: 25%;
        transform: translateX(-15%);
      }
    }

    &__button {
      background-color: #0D7DFC;
      background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M60.5102 68.0604L60.8295 56.5178L50.6737 62.0125L60.5102 68.0604ZM40.1205 32.4759L55.348 60.6206L57.107 59.6688L41.8795 31.5241L40.1205 32.4759Z' fill='white'/%3E%3C/svg%3E%0A");
      box-shadow: 0px 10px 75px 0px #7C4DC287;
    }
  }
}

.fds-info {
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;

  &__inner {
    position: relative;
    display: flex;
    justify-content: center;
    height: 100%;
    box-sizing: border-box;
    padding-top: 218px;

    @include media(from_l) {
      display: block;
      padding-top: 374px;
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #003399;
    background-size: 1457px auto;
    background-repeat: no-repeat;
    background-position: left 50% top 512px;

    @include media(from_l) {
      background-size: 1314px auto;
      background-repeat: no-repeat;
      background-position: right -500px top -390px;
    }
  }

  &__bg_type_mob {
    @include media(from_l) {
      display: none;
    }
  }

  &__bg_type_desk {
    display: none;

    @include media(from_l) {
      display: block;
    }
  }

  &__box {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include media(from_l) {
      flex-direction: row;
    }
  }

  &__logo {
    margin: 0 0 40px;

    @include media(from_l) {
      margin: 0 70px 0 0;
    }
  }

  
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 356px;

    @include media(from_l) {
      display: block;
    }
  }

  &__desc {
    margin: 0 0 30px;
    font-family: "Roboto";
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;

    @include media(from_l) {
      text-align: left;
    }
  }

  &__items {
    display: flex;
    justify-content: center;
    margin: 0 0 0 -40px;

    @include media(from_l) {
      justify-content: flex-start;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    margin: 0 0 0 40px;

    font-family: "Roboto";
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
  }

  &__item-title {
    margin: 0 0 16px;
    font-weight: 500px;
  }

  &__item-link {
    color: inherit;

    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .5;
    }
  }
}

.fds-header {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
  padding: 10px;

  &__step {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 57px;
    min-height: 57px;
    margin: -10px 40px 0 0;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: #fff;
      box-shadow: -2px 35px 35px rgba(45, 135, 219, .08), 1px 0px 13px rgba(45, 135, 219, .12), -1px 3px 5px rgba(45, 135, 219, .08);
      border-radius: 5px;
      transform: rotate(-45deg);
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 1;
      width: calc(100% - 12px);
      height: calc(100% - 12px);
      background: #0D7DFC;
      box-shadow: 22px 36px 112px rgba(144, 144, 144, .11), 11px 18px 49px rgba(144, 144, 144, .08), 4px 7px 18.2px rgba(144, 144, 144, .05), 1px 1px 6px rgba(144, 144, 144, .04);
      border-radius: 5px;
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }

  &__step-value {
    position: relative;
    z-index: 2;
    font-family: "Roboto";
    font-weight: 700;
    font-size: 26px;
    line-height: 26px;
    text-transform: uppercase;
    color: #fff;
  }
  
  &__title {
    margin: 0;
    font-family: "Roboto";
    font-weight: 700;
    font-size: 36px;
    line-height: 42px;
    text-transform: uppercase;
    color: #262626;
  }
}

.fds-typography {

  &__inner {
    box-sizing: border-box;
    background-color: #fff;
    padding: 60px 0;

    @include media(from_l) {
      padding: 100px;
    }
  }

  &__header {
    margin: 0 0 60px;
  }

  &__box {
    display: flex;
    align-items: flex-start;
  }

  &__font {
    width: 278px;
    height: 308px;
    margin-right: 105px;

    @include media(from_l) {
      width: 326px;
      height: 358px;
      margin-right: 165px;
    }
  }

  &__colors {
    display: flex;
    flex-direction: column;
  }

  &__color {
    box-sizing: border-box;
    padding: 30px;
    background-color: #fff;
    box-shadow: -1.79875px 34.8849px 34.875px rgba(45, 135, 219, .08), 1px 0px 13px rgba(45, 135, 219, .12), -1px 3px 4.625px rgba(45, 135, 219, .08);
    border-radius: 15px;

    font-family: "Roboto";
    font-weight: 400;
    font-size: 18px;
    line-height: 132%;
  }

  &__color:nth-child(1) {
    width: 363px;
    height: 234px;
    color: #003399;
    background-color: #fff;

    @include media(from_l) {
      width: 506px;
      height: 283px;
    }
  }

  &__color:nth-child(2) {
    width: 292px;
    height: 202px;
    margin-top: -140px;
    margin-left: -48px;
    background-color: #003399;
    color: #fff;

    @include media(from_l) {
      width: 351px;
      height: 243px;
      margin-top: -170px;
      margin-left: -56px;
    }
  }

  &__color:nth-child(3) {
    align-self: center;
    width: 259px;
    height: 167px;
    margin-top: -100px;
    background-color: #0D7DFC;
    color: #fff;

    @include media(from_l) {
      width: 311px;
      height: 202px;
      margin-top: -120px;
    }
  }
}

.fds-proto {

  &__inner {
    box-sizing: border-box;
    background-color: #fff;
    padding: 60px 0 0;

    @include media(from_l) {
      padding: 100px 0 0;
    }
  }

  &__header {
    margin: 0 0 40px;

    @include media(from_l) {
      margin: 0 0 60px;
    }
  }

  &__screen {
    display: flex;
    justify-content: center;
    align-self: center;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__screen_type_proto {
    padding: 48px 0;
    margin: 0 0 60px;
    background-size: auto 468px;

    @include media(from_l) {
      padding: 54px 0 84px;
      margin: 0 0 100px;
      background-size: 2227px auto;
    }
  }

  &__screen_type_design {
    position: relative;
    box-sizing: border-box;
    padding: 218px 0;
    margin: 0 30px;
    background-size: 2433px auto;
    background-color: #2D87DB;

    @include media(from_l) {
      padding: 180px 0;
      margin: 0 0 100px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;  
      background-color: #2D87DB;
      opacity: .2;
    }
  }

  &__screen-pic-box {
    box-sizing: border-box;
    border-radius: 20px;
    overflow: hidden;
  }

  &__screen_type_proto  &__screen-pic-box {
    width: 632px;
    height: 377px;
    border: 3px solid #F7F7F7;

    @include media(from_l) {
      width: 944px;
      height: 563px;
    }
  }

  &__screen_type_design &__screen-pic-box {
    position: relative;
    z-index: 1;
    width: 737px;
    height: 460px;

    @include media(from_l) {
      width: 1031px;
      height: 643px;
    }
  }

  &__screen-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.fds-mobile {

  &__inner {
    box-sizing: border-box;
    padding: 60px 0 100px;

    @include media(from_l) {
      padding: 100px 0;
    }
  }
  
  .container {
    padding: 0 60px;

    @include media(from_l) {
      padding: 0;
      max-width: 1000px;
    }
  }

  &__header {
    margin-bottom: 20px;

    @include media(from_l) {
      margin-bottom: 50px;
    }
  }

  &__screens {
    display: flex;
    width: calc(100% + 20px);
    transform: translateX(-5px);
    margin: 0 0 0 -10px;

    @include media(from_l) {
      transform: translateX(-10px);
    }
  }

  &__screen {
    flex-basis: calc(33.333% - 10px);
    height: 444px;
    box-sizing: border-box;
    border: 10px solid #F2F2F2;
    margin-left: 10px;
    border-radius: 20px;
    overflow: hidden;

    @include media(from_l) {
      height: 590px;
    }
  }

  &__screen:nth-child(even) {
    margin-top: 64px;

    @include media(from_l) {
      margin-top: 150px;
    }
  }

  &__screen-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
