@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.wd-screen-intro {
  background-color: #7C4DC2;

  .screen-intro {

    &__bg {
      background-color: #7C4DC2;
    }

    &__header {
      margin-bottom: 76px;
      margin: 0 0 55px;

      @include media(from_l) {
        position: relative;
        justify-content: flex-start;
        max-width: unset;
        padding: 0;
        margin: 0;
        transform: translateY(200px);
      }
    }

    &__info {
      @include media(from_l) {
        flex-basis: calc(25% + 40px);
        flex-direction: column;
        padding-left: 0;
      }
    }


    &__title {
      flex-direction: row;
      margin-bottom: 55px;

      @include media(from_l) {
        flex-direction: column;
        flex-basis: 25%;
        align-items: flex-start;
        margin-top: -6px;
        margin-bottom: 0;
        color: #F2B94B;
      }
    }

    &__title-part_type_stroke {
      -webkit-text-stroke: 1px #F2B94B;
    }

    &__thumb {
      background-color: #F2B94B;

      @include media(from_l) {
        margin-left: 50%;
      }
    }

    &__button-container {
      @include media(from_l) {
        right: 14%;
      }
    }

    &__button {
      background-color: #fff;
      background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M40.5 68.507L50.5 62.7335L40.5 56.96L40.5 68.507ZM60.134 32.5L44.134 60.2128L45.866 61.2128L61.866 33.5L60.134 32.5Z' fill='%23F2B94B'/%3E%3C/svg%3E%0A");
      box-shadow: 0px 10px 75px 0px #7C4DC287;

      transform: rotate(-66deg);

      @include media(from_l) {
        transform: rotate(0);
      }
    }
  }
}

.wd-screen-picture {
  position: relative;
  background-color: #E1A014;

  .screen-picture {

    &__logo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.wd-info {

  &__inner {
    position: relative;
    height: 100vh;
    background-color: #7C4DC2;

    &::before {
      content: "";
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;

      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      background-image: url(../../../assets/media/wd-pic-info-bg-1.png);
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;

      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      background-image: url(../../../assets/media/wd-pic-info-bg-3.png);
    }
  }

  &__pic-bg {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  &__box {
    position: absolute;
    z-index: 4;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 577px;
    height: 577px;
    box-sizing: border-box;
    border: 5px solid #7C4DC2;
    background-color: #F2B94B;
    border-radius: 50%;
  }

  &__box-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 358px;
  }

  &__logo {
    margin: 0 0 20px;
  }

  &__desc {
    margin: 0 0 30px;
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 151.69%;
    text-align: center;
    color: #262626;
  }

  &__items {
    display: flex;
    margin: 0 0 0 -30px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 0 30px;
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #262626;
  }

  &__item-title {
    margin: 0 0 10px;
    font-weight: 700;
    color: #7C4DC2;
  }

  &__item-link {
    color: inherit;

    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .5;
    }
  }
}

.wd-typography {

  &__inner {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    padding: 130px 204px 260px 0;

    background-color: #262626;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url(../../../assets/media/wd-pic-typography-bg-1.svg);

    
    @include media(from_l) {
      padding: 180px 0 280px 0;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      background-image: url(../../../assets/media/wd-pic-typography-bg-2.png);
    }
  }

  &__content {
    position: relative;
    z-index: 2;
  }

  &__title {
    width: 393px;
    height: 35px;

    margin: 0 0 80px;

    background-repeat: no-repeat;
    background-position: left 0 top 50%;
    background-size: contain;

    @include media(from_l) {
      width: 393px;
      height: 48px;
    }
  }

  &__row {
    display: flex;
    flex-direction: column;

    @include media(from_l) {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__colors {
    display: flex;
    box-sizing: border-box;
    margin: 0 0 0 -20px;
    padding: 0 0 90px;

    @include media(from_l) {
      padding: 0;
    }
  }

  &__color {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 0 0 20px;
  }

  &__color-pic {
    width: 97px;
    height: 97px;
    margin: 0 0 12px;

    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__color-title {
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;

    color: #D9D9D9;
  }

  &__font {
    width: 590px;
    height: 218px;

    background-size: contain;
    background-position: 0 0;
    background-repeat: no-repeat;

    @include media(from_l) {
      width: 590px;
      height: 212px;
    }
  }
}

.wd-desk {
  position: relative;
  z-index: 1;

  &__inner {
    margin-top: -127px;
    margin-bottom: -167px;

    @include media(from_l) {
      margin-top: -143px;
      margin-bottom: -199px;
    }
  }

  &__pic-wrapper {
    width: 651px;
    height: 2120px;
    margin: 0 auto;
    border-radius: 30px;
    overflow: hidden;
    box-shadow: 0px 4px 46px 0px #7C4DC226;

    @include media(from_l) {
      width: 778px;
      height: 2534px;
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.wd-mob {

  &__inner {
    background-color: #fff;
  }

  &__section-main {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 290px 0 123px 0;
    background-color: #7C4DC2;

    @include media(from_l) {
      padding: 350px 0;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 590px;
      height: 890px;

      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      background-image: url(../../../assets/media/wd-pic-mob-bg.svg);
    }
  }

  &__section-secondary {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 1114px;
    overflow: hidden;
    background-color: #F2B94B;

    @include media(from_l) {
      max-width: $mainContentWidth_desktop;
      margin: -240px auto 0;
      // transform: translateY(-240px);
      border-radius: 120px;
    }
  }

  &__pic {
    object-fit: cover;
  }

  &__section-main &__pic {
    position: relative;
    z-index: 1;
    width: 345px;
    height: 670px;
  }

  &__section-secondary &__pic {
    width: 1735px;
    height: 2100px;

    transform: translate(100px, 100px);
  }
}

.wd-pages {

  &__inner {
    height: 731px;
    padding: 150px 0 140px;

    @include media(from_l) {
      height: 1010px;
      padding: 120px 0 200px;
    }
  }

  &__container {
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      border-radius: 50%;
      opacity: .5;
      filter: blur(167px);
    }

    &::before {
      top: 75px;
      right: 0;
      
      width: 322px;
      height: 322px;

      background: #E1A014;

      @include media(from_l) {
        top: 143px;
        right: 144px;

        width: 393px;
        height: 392px;
      }
    }

    &::after {
      top: 265px;
      right: 300px;

      width: 378px;
      height: 351px;

      background: #A53782;

      @include media(from_l) {
        top: 535px;
        right: 670px;

        width: 441px;
        height: 408px;
      }
    }
  }

  &__pic-box {
    position: absolute;
    width: 650px;
    height: 397px;
    border-radius: 30px;
    overflow: hidden;

    @include media(from_l) {
      width: 954px;
      height: 582px;

      border-radius: 50px;
    }
  }

  &__pic-box_pos_left {
    top: 0;
    left: -95px;

    @include media(from_l) {
      left: -200px;
    }
  }

  &__pic-box_pos_right {
    top: 334px;
    right: -195px;

    @include media(from_l) {
      top: 427px;
      right: -260px;
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
