@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.card-price {
  
  &__inner {
    position: relative;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    padding: 30px 25px;
    background: #FFFFFF;
    border: 1px solid #D2D2D2;
    border-radius: 10px;
    overflow: hidden;
  }

  &__title {
    display: flex;
    align-items: flex-start;
    flex-basis: 215px;
    flex-shrink: 0;
    margin: 0 24px 0 0;
    font-family: "IBM Plex Mono";
    font-weight: 600;
    font-size: 22px;
    line-height: 29px;
    color: #262626;

    @include media(from_l) {
      flex-basis: 280px;
      font-size: 25px;
      line-height: 32px;
    }

    &::before {
      content: "";
      flex-shrink: 0;
      width: 35px;
      height: 20px;
      margin: 4px 5px 0 0;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml,%3Csvg width='35' height='21' viewBox='0 0 35 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 12.2539V9.02673L9.59076 3.64811V6.87528L2.96644 10.5468V10.7339L9.59076 14.4053V17.6325L0 12.2539Z' fill='%23C34F44'/%3E%3Cpath d='M19.5966 21L12.437 0H15.4034L22.563 21H19.5966Z' fill='%23C34F44'/%3E%3Cpath d='M25.4092 14.4053L32.0336 10.7339V10.5468L25.4092 6.87528V3.64811L35 9.02673V12.2539L25.4092 17.6325V14.4053Z' fill='%23C34F44'/%3E%3C/svg%3E%0A");
    
      @include media(from_l) {
        width: 40px;
        height: 22px;
        margin: 5px 10px 0 0;
      }
    }
  }

  &__content {
    position: relative;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    @include media(from_l) {
      flex-direction: column;
      align-items: flex-start;
      width: 835px;
    }
  }

  &__desc {
    margin: 0 0 10px;
    font-family: "IBM Plex Mono";
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    color: #262626;

    @include media(from_l) {
      flex-basis: unset;
      max-width: 500px;
      font-size: 16px;
    }
  }

  &__rules-list {
    flex-shrink: 0;
    flex-basis: 185px;
    margin: 0 20px 0 0;
    padding: 0;
    list-style: none;

    @include media(from_l) {
      flex-basis: unset;
      max-width: 500px;
    }
  }

  &__rules-item {
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;   
    color: #595959;

    @include media(from_l) {
      font-size: 16px;
    }
  }

  &__rules-item + &__rules-item {
    margin-top: 10px;
  }

  &__info-list {
    flex-shrink: 0;
    flex-basis: 255px;
    margin: 0;
    padding: 0;
    list-style: none;

    @include media(from_l) {
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  &__info-item {
    display: flex;
    align-items: center;
    font-family: "IBM Plex Mono";
    font-weight: 700;
    font-size: 20px;
    line-height: 26px; 
    letter-spacing: -.03em;

    @include media(from_l) {
      font-size: 25px;
      line-height: 32px;
    }
  }

  &__price {
    color: $red;
  }

  &__button {
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    margin: 0 0 0 10px;
    padding: 0;
    border-radius: 50%;
    border: none;
    background-color: #000;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='50' height='50' viewBox='0 0 50 50' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M32 12.9999L22 18.7734L32 24.547L32 12.9999ZM18.866 37.7487L28.366 21.2942L26.634 20.2942L17.134 36.7487L18.866 37.7487Z' fill='white'/%3E%3C/svg%3E%0A");
    cursor: pointer;
    transition: $transition_main;
    
    @include media(from_l) {
      width: 60px;
      height: 60px;
      margin: 0 0 0 20px;
    }
  }

  .button-container:hover &__button,
  .button-container:active &__button {
    background-color: $red;
  }

  &__pic-wrapper {
    position: absolute;
    left: -20px;
    bottom: -55px;
    width: 224px;
    height: 130px;
    border-radius: 10px;
    overflow: hidden;
    transform: rotate(15deg);

    @include media(from_l) {
      width: 304px;
      height: 172px;
      bottom: -90px;
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
