@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.bmw-screen-intro {

  .screen-intro {

    &__header {
      margin-bottom: 76px;
      margin: 0 0 88px 50%;

      @include media(from_l) {
        position: relative;
        flex-direction: column;
        justify-content: flex-start;
        max-width: 50%;
        padding: 0;
        margin: 0;
        transform: translateY(100%);
        height: 0;
      }
    }

    &__info {
      @include media(from_l) {
        flex-basis: calc(25% + 40px);
        flex-direction: column;
        padding-left: 0;
      }
    }


    &__title {
      margin-bottom: 55px;
      margin-left: auto;
      transform: translateX(-50%);
      color: #FFFFFF;

      @include media(from_l) {
        align-items: flex-start;
        order: unset;
        margin-bottom: 65px;
        margin-left: 0;
        transform: translateX(0);
      }
    }


    &__title-part_type_stroke {
      -webkit-text-stroke: 2px #B84236;
      transform: translateX(80px);

      @include media(from_l) {
        transform: translateX(306px);
      }
    }

    &__thumb {
      background-color: #B84236;

      @include media(from_l) {
        margin-left: 75%;
      }
    }

    &__button-container {
      @include media(from_l) {
        right: 50%;
      }
    }

    &__button {
      background-color: #D95B4F;
      background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M60.5102 68.0604L60.8295 56.5178L50.6737 62.0125L60.5102 68.0604ZM40.1205 32.4759L55.348 60.6206L57.107 59.6688L41.8795 31.5241L40.1205 32.4759Z' fill='white'/%3E%3C/svg%3E%0A");
    }
  }
}


.bmw-screen-picture {
  position: relative;

  .screen-picture {

    &__logo {
      position: static;
      width: 100%;
      margin: 0 0 40px;

      @include media(from_l) {
        margin: 0 0 60px;
      }
    }
  }

  &__box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 593px;

    @include media(from_l) {
      width: 655px;
    }
  }
}

.bmw-header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto 40px;

  @include media(from_l) {
    margin: 0 auto 60px;
  }

  &__title {
    margin: 0;
    font-family: "Roboto";
    font-weight: 500;
    font-size: 26px;
    line-height: 30px;
    color: #CCCCCC;
  }

  &__translate {
    font-family: "Roboto";
    font-weight: 400;
    font-size: 26px;
    line-height: 30px;
    color: #7E7E7E;
  }
}

.bmw-info {

  &__desk {
    margin: 0 0 40px;
    font-family: "Roboto";
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    color: #FFFFFF;
  }

  &__items {
    display: flex;
    justify-content: center;
    margin: 0 0 0 -40px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 0 40px;

    font-family: "Roboto";
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    color: #fff;
  }

  &__item-title {
    margin: 0 0 16px;
    font-weight: 500;
  }

  &__item-link {
    color: inherit;
    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .5;
    }
  }
}

.bmw-index {

  &__inner {
    background-color: #262626;
    padding: 60px 0 0;
    margin-bottom: -256px;

    @include media(from_l) {
      padding: 100px 0 0;
      margin-bottom: -367px;
    }
  }

  &__screens {
    position: relative;
    width: 100%;
    max-width: 690px;
    margin: 0 auto;

    @include media(from_l) {
      max-width: 1000px;
    }
  }

  &__screen {
    box-sizing: border-box;
    border: 2px solid #B3B3B3;
    border-radius: 20px;
    overflow: hidden;
  }

  &__screen_type_desk {
    width: 100%;
    height: 2604px;

    @include media(from_l) {
      height: 3675px;
    }
  }

  &__screen_type_mob {
    position: absolute;
    top: 90px;
    right: -72px;
    width: 137px;
    height: 1974px;

    @include media(from_l) {
      right: -102px;
      width: 194px;
      height: 2786px;
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.bmw-typography {

  &__inner {
    padding: 330px 0 60px;
    background-color: #0D0D0D;

    @include media(from_l) {
      padding: 470px 0 120px;
    }
  }

  &__row {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include media(from_l) {
      margin: 0 auto;
    }
  }

  &__font {
    flex-shrink: 0;
    width: 269px;
    height: 150px;

    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;

    @include media(from_l) {
      width: 287px;
      height: 164px;
    }
  }

  &__colors {
    display: flex;
    align-items: center;
    transform: translateX(70px);

    @include media(from_l) {
      transform: translateX(90px);
    }
  }

  &__color {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-family: "Roboto";
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #fff;
  }

  &__color:nth-child(1) {
    width: 184px;
    height: 184px;
    background-color: #EB5757;

    @include media(from_l) {
      width: 218px;
      height: 218px;
    }
  }

  &__color:nth-child(2) {
    width: 151px;
    height: 151px;
    background-color: #262626;
    transform: translate(-40px);

    @include media(from_l) {
      width: 178px;
      height: 178px;
      transform: translate(-48px);
    }
  }

  &__color:nth-child(3) {
    width: 128px;
    height: 128px;
    background-color: #191919;
    transform: translate(-55px);

    @include media(from_l) {
      width: 152px;
      height: 152px;
      transform: translate(-78px);
    }
  }
}

.bmw-screen-picture_fixed_height {
  width: 100%;
  height: 700px;
  min-height: 700px;
  max-height: 700px;
}

.bmw-pages {

  &__inner {
    padding: 60px 0;
    background-color: #0D0D0D;

    @include media(from_l) {
      padding: 120px 0;
    }
  }

  &__screen-box {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 575px;
    margin: -30px 0 0;

    @include media(from_l) {
      width: 876px;
      margin: -100px 0 0;
      transform: translateX(-244px);
    }
  }

  &__screen {
    box-sizing: border-box;
    border: 2px solid #B3B3B3;
    border-radius: 30px;
    overflow: hidden;

    margin: 30px 0 0;

    @include media(from_l) {
      margin: 100px 0 0;
    }
  }

  &__screen:nth-child(1) {
    width: 100%;
    height: 359px;
    opacity: .8;

    @include media(from_l) {
      width: 100%;
      height: 547px;
    }
  }

  &__screen:nth-child(2) {
    width: 100%;
    height: 592px;
    opacity: .8;

    @include media(from_l) {
      height: 902px;
    }
  }

  &__screen:nth-child(3) {
    position: absolute;
    top: 180px;
    left: 364px;
    width: 575px;
    height: 653px;

    @include media(from_l) {
      top: 275px;
      left: 555px;
      width: 876px;
      height: 994px;
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.bmw-mobile {

  &__inner {
    background-color: #0D0D0D;
  }

  &__row {
    display: flex;
  }

  &__col {
    display: flex;
    align-items: center;
    flex-basis: 50%;
    box-sizing: border-box;
    padding: 36px 110px;

    @include media(from_l) {
      padding: 82px 256px;
    }
  }

  &__col:nth-child(1) {
    justify-content: flex-end;
    background-color: #262626;
  }

  &__col:nth-child(2) {
    justify-content: flex-start;
    background-color: #EB5757;
  }
  

  &__screen {
    width: 198px;
    height: 376px;
    box-sizing: border-box;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 82px 58px 80px 0px #00000021;

    @include media(from_l) {
      width: 250px;
      height: 475px;
    }
  }

  &__col:nth-child(1) &__screen {
    border: 2px solid #B3B3B3;
  }

  &__col:nth-child(2) &__screen {
    border: 2px solid #262626;
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
