@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.bgd-screen-intro {
  background-color: #0FA068;

  .screen-intro {

    &__bg {
      background-color: #0FA068;
    }

    &__header {
      max-width: 550px;

      @include media(from_l) {
        flex-direction: column;
        align-items: flex-end;
        max-width: unset;
      }
    }

    &__title {
      @include media(from_l) {
        order: unset;
        margin: 0 0 22px;
      }
    }

    &__info {
      padding-left: 25px;

      @include media(from_l) {
        padding-left: 0;
      }
    }

    &__button {
      background-color: #F08365;
      background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M60.5102 68.0604L60.8295 56.5178L50.6737 62.0125L60.5102 68.0604ZM40.1205 32.4759L55.348 60.6206L57.107 59.6688L41.8795 31.5241L40.1205 32.4759Z' fill='white'/%3E%3C/svg%3E%0A");
    }
  }
}

.bgd-title {
  font-family: "IBM Plex Mono";
  font-weight: 700;
  font-size: 25px;
  line-height: 32px;
  color: #262626;

  @include media(from_l) {
    font-size: 36px;
    line-height: 47px;
  }
}

.bgd-info {
  position: relative;
  height: 868px;
  background-color: #F2F2F2;

  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 20px;
    width: 50%;
    height: 100%;
    background-color: #0FA068;
    background-image: url(../../../assets/media/bgd-pic-info-bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;

    @include media(from_l) {
      top: 0;
    }
  }

  &__inner {
    display: flex;
    width: 100%;
    max-width: 735px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 20px 0 0 20px;

    @include media(from_l) {
      padding: 0 20px;
      max-width: 910px;
    }
  }

  &__logo {
    flex-basis: 50%;
    padding: 105px 0 0;
  }

  &__content {
    position: relative;
    z-index: 1;
    flex-basis: 50%;
    box-sizing: border-box;
    padding: 130px 40px 0 30px;

    @include media(from_l) {
      padding: 110px 60px 0;
    }
  }

  &__project-title {
    margin: 0 0 30px;
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 18px;
    line-height: 131.69%;

    color: #FFFFFF;
  }

  &__items {
    display: flex;
    justify-content: flex-start;
    margin: 0 0 0 -35px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    margin: 0 0 0 35px;

    font-family: "IBM Plex Mono";
    font-size: 16px;
    line-height: 21px;
    color: #fff;
  }

  &__item-title {
    margin: 0 0 10px;
    font-weight: 500;
  }

  &__item-link {
    color: #fff;

    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .5;
    }
  }
}

.bgd-desk {
  height: 1866px;
  box-sizing: border-box;
  padding: 0 0 60px;
  background-color: #F2F2F2;

  @include media(from_l) {
    height: 2567px;
    background-color: #fff;
  }

  &__pic-wrapper {
    position: relative;
    width: 100%;
    max-width: 735px;
    height: 377px;
    box-sizing: border-box;
    border: 4px solid #FFFFFF;
    border-radius: 30px;
    margin: 0 auto;
    transform: translateY(-565px);

    @include media(from_l) {
      max-width: 910px;
      height: 473px;
      transform: translateY(-506px);
    }
  }

  &__pic {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    width: auto;
    height: 2341px;
    border-radius: 25px;
    box-shadow: 22.15964698791504px 35.67842483520508px 112px 0px #9090901C;

    @include media(from_l) {
      height: 2942px;
    }
  }
}

.bgd-typography {
  padding: 60px 0;
  background-color: #99C6B5;

  @include media(from_l) {
    padding: 120px 0;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
  }

  &__row + &__row {
    margin-top: 30px;

    @include media(from_l) {
      margin-top: 45px;
    }
  }

  &__title {
    font-family: "IBM Plex Mono";
    font-weight: 700;
    font-size: 25px;
    line-height: 32px;
    color: #FFFFFF;

    @include media(from_l) {
      font-size: 36px;
      line-height: 47px;
    }
  }

  &__font-example {
    display: flex;
    justify-content: flex-start;
    flex-basis: 65%;
    height: 130px;
    background-repeat: no-repeat;
    background-size: auto 100%;

    @include media(from_l) {
      height: 175px;
    }
  }

  &__colors {
    display: flex;
    flex-basis: 65%;
  }

  &__color {
    position: relative;
    width: 25%;
    height: 0;
    padding: 0 0 25%;
  }

  &__color-title {
    position: absolute;
    top: 10px;
    left: 10px;
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #fff;
  }

  &__color:nth-child(1) {
    background-color: #F08365;
  }

  &__color:nth-child(2) {
    background-color: #5FA494;
  }

  &__color:nth-child(3) {
    background-color: #E6BE80;
  }

  &__color:nth-child(4) {
    background-color: #fff;
  }

  &__color:nth-child(3) &__color-title,
  &__color:nth-child(4) &__color-title {
    color: #262626;
  } 
}

.bgd-pages {
  background-color: #f2f2f2;

  &__inner {
    position: relative;
    width: 100%;
    max-width: 1092px;
    padding: 60px 0;
    margin: 0 auto;

    &::before {
      content: "";
      position: absolute;
      top: 60px;
      right: -20px;
      width: 282px;
      height: 215px;
        
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      background-image: url(../../../assets/media/bgd-pic-pages-bg.svg);

      @include media(from_l) {
        right: 0;
      }
    }
  }

  &__title {
    margin: 0 0 60px;

    @include media(from_l) {
      padding: 10px 0 10px 32px;
      margin: 0 0 150px;
    }
  }

  &__items {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    margin: -30px 0 0;

    @include media(from_l) {
      margin: -46px 0 0;
    }
  }

  &__item-row {
    display: flex;
    margin: 30px 0 0;

    @include media(from_l) {
      margin: 46px 0 0;
    }
  }

  &__item-row:nth-child(odd) {
    justify-content: flex-end;

    @include media(from_l) {
      justify-content: center;
    }
  }

  &__item-row:nth-child(even) {
    justify-content: flex-start;
  }

  &__item {
    width: 590px;
    height: 320px;
    box-sizing: border-box;
    padding: 20px;
    border: 4px solid #fff;
    border-radius: 35px;

    @include media(from_l) {
      width: 630px;
      height: 360px;
    }
  }

  &__item-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 25px;
  }
}

.bgd-forms {
  background-color: #99C6B5;

  &__inner {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 930px;
    margin: 0 auto;
    padding: 90px 0;
  }

  &__pic-wrapper {
    flex-basis: calc(50% - 10px);
    height: 300px;

    @include media(from_l) {
      flex-basis: calc(50% - 15px);
      height: 376px;
    }
  }

  &__pic {
    width: 100%;
    height: auto;
  }
}

.bgd-mobile {
  padding: 60px 0 100px;
  background-color: #f2f2f2;

  @include media(from_l) {
    padding: 60px 0 160px;
  }

  &__inner {
    position: relative;
    box-sizing: border-box;
    padding: 0 35px;

    @include media(from_l) {
      padding: 0;
      margin-left: -20px;
      margin-right: -90px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      z-index: 1;
      width: 396px;
      height: 68px;
      background-image: url(../../../assets/media/bgd-pic-mobile-bg.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;

      @include media(from_l) {
        right: -180px;
        width: 764px;
        height: 131px;
      }
    }
  }

  &__title {
    margin: 0 0 38px;

    @include media(from_l) {
      margin: 0 0 67px;
    }
  }

  &__pics {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: -30px 0 0 -30px;
    padding-bottom: 170px;

    @include media(from_l) {
      padding-bottom: 0;
    }
  }

  &__pic-wrapper {
    flex-basis: calc(50% - 30px);
    height: 568px;
    box-sizing: border-box;
    padding: 20px;
    margin: 30px 0 0 30px;
    border: 4px solid #FFFFFF;
    border-radius: 35px;

    @include media(from_l) {
      flex-basis: calc(25% - 30px);
    }
  }

  &__pic-wrapper:nth-child(even) {
    transform: translateY(170px);

    @include media(from_l) {
      margin-top: 196px;
      transform: translateY(0);
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 25px;
  }
}
