@import "@theme/vars.scss";

@mixin media($point) {
  @if $point == "" {
    @media only screen {
      @content;
    }
  }

  // uniq breakpoints
  // - @else if $point === '...' {}

  // limited breakpoints
  @else if $point == only_s {
    @media only screen and (min-width: 0) and (max-width: $firstWidth_pad - 1) {
      @content;
    }
  }

  @else if $point == only_m {
    @media only screen and (min-width: $firstWidth_pad) and (max-width: $firstWidth_desktop - 1) {
      @content;
    }
  }

  @else if $point == only_l {
    @media only screen and (min-width: $firstWidth_desktop) and (max-width: $firstWidth_desktop-large - 1) {
      @content;
    }
  }

  @else if $point == only_xl {
    @media only screen and (min-width: $firstWidth_desktop-large) and (max-width: $firstWidth_desktop-extra-large - 1) {
      @content;
    }
  }

  // mobile first
  @if $point == from_m {
    @media only screen and (min-width: $firstWidth_pad) {
      @content;
    }
  }

  @else if $point == from_l {
    @media only screen and (min-width: $firstWidth_desktop) {
      @content;
    }
  }

  @else if $point == from_xl {
    @media only screen and (min-width: $firstWidth_desktop-large) {
      @content;
    }
  }

  @else if $point == from_xxl {
    @media only screen and (min-width: $firstWidth_desktop-extra-large) {
      @content;
    }
  }

  // desktop first
  @else if $point == to_m {
    @media only screen and (max-width: $firstWidth_pad) {
      @content;
    }
  }

  @else if $point == to_l {
    @media only screen and (max-width: $firstWidth_desktop) {
      @content;
    }
  }

  @else if $point == to_xl {
    @media only screen and (max-width: $firstWidth_desktop-large) {
      @content;
    }
  }

  @else if $point == to_xxl {
    @media only screen and (max-width: $firstWidth_desktop-extra-large) {
      @content;
    }
  }
}
