@import "@theme/vars.scss";

.list {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    margin-bottom: 15px;

    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        color: #000;
        padding: 0 0 0 28px;

        &:last-child{
            margin-bottom: 0;
        }

        &::before{
            content: "";
            position: absolute;
            display: flex;
            flex-shrink: 0;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: #262626;
            top: 10px;
            left: 12px;
        }

        p{
            margin-bottom: 5px;

            &:last-child{
            margin-bottom: 0;
            }
        }
    }
}