@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.mf-cursor {
  z-index: 10000;

  &::before {
    @include media(from_m) {
      transform: scale(0);
      background-color: transparent;
      border: 3px solid $blue;

      transition: $transition_main;
    }
  }
}

[data-cursor="-menu"] {
  @include media(from_m) {
    cursor: none;
  }
}

.mf-cursor.-menu {

  .mf-cursor-media {
    @include media(from_m) {
      width: 180px;
      height: 180px;
      margin: -90px 0 0 -90px;
    }
  }

  .mf-cursor-media-box {
    @include media(from_m) {
      box-sizing: border-box;
      border: 3px solid $yellow;
      background-color: #fff;
    }
  }

  &::before {
    @include media(from_m) {
      transform: scale(1);
      background-color: transparent;
      border: 3px solid $yellow;

      transition: $transition_main;
    }
  }

  &.-hidden::before {
    @include media(from_m) {
      transform: scale(0);
    }
  }
}

[data-cursor="-project"] {
  @include media(from_m) {
    cursor: none;
  }
}

.mf-cursor.-project {

  &::before {
    @include media(from_m) {
      transform: scale(1);
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background-color: #32808D;
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: center;
      background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M60 33L50 38.7735L60 44.547L60 33ZM40.866 68.141L56.366 41.2942L54.634 40.2942L39.134 67.141L40.866 68.141Z' fill='white'/%3E%3C/svg%3E%0A");

      transition: $transition_main;
    }
  }
}

[data-cursor="-slider"] {
  @include media(from_m) {
    cursor: none;
  }
}

.mf-cursor.-slider {

  &::before {
    @include media(from_m) {
      transform: scale(1);
      width: 100px;
      height: 100px;
      border-radius: 50%;
      border-color: #262626;
      background-color: #262626;
      background-repeat: no-repeat;
      background-size: 56px auto;
      background-position: center;
      background-image: url("data:image/svg+xml,%3Csvg width='53' height='28' viewBox='0 0 53 28' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M37 4L47 14L37 24L39 28L53 14L39 0L37 4Z' fill='white'/%3E%3Cpath d='M16 4L6 14L16 24L14 28L0 14L14 0L16 4Z' fill='white'/%3E%3C/svg%3E%0A");

      transition: $transition_main;
    }
  }
}

