@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

html {
  font-family: $defaultFontFamily;
  font-size: $defaultFontSize;
  line-height: $defaultLineHeight;
  color: $defaultTextColor;
  background-color: $defaultBackgroundColor;
  // cursor: none;
}

body{
  overflow: hidden auto;
}

.container {
  width: 100%;
  max-width: $mainContentWidth_pad;

  box-sizing: border-box;
  padding: 0 40px;
  margin: 0 auto;

  @include media(from_l) {
    max-width: $mainContentWidth_desktop;
    padding: 0;
  }
}

.lozad,
.swiper-lazy {
  opacity: 0;
  transition: 1s ease-out;
}

.lozad[data-loaded="true"],
.swiper-lazy-loaded {
  opacity: 1;
}

.code {
  color: #F08F3B;
}

input,
button,
select {
  outline-offset: 4px;
}

._img-fit-contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
