@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.r-screen-intro {
  background-color: #F2B94B;

  .screen-intro {

    &__bg {
      background-color: #F2B94B;
    }

    &__title {
      color: #000;
    }
  
    &__title-part_type_stroke {
      -webkit-text-stroke: 1px #262626;
    }

    &__info-item {
      color: #262626;
    }

    &__thumb {
      margin-left: 0;
      background-color: #262626;
      
      @include media(from_l) {
        margin-left: 25%;
      }
    }
  }
}

.r-title {
  display: flex;
  justify-content: space-between;
  font-family: "IBM Plex Mono";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
}

.r-title_light {
  color: #fff;
}

.r-info {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 60px 0;
  background: linear-gradient(359.82deg, #292929 -11.66%, rgba(41, 41, 41, .56) 20.2%, rgba(41, 41, 41, 0) 63.01%),;

  &__row {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include media(from_l) {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end;
    }
  }

  &__logo {
    margin: 0 0 20px;

    @include media(from_l) {
      margin: 0;
    }
  }

  &__logo-pic {
    width: 378px;
    height: 52px;
    margin: 0 0 10px;

    @include media(from_l) {
      margin: 0 0 15px;
    }
  }

  &__logo-label {
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;

    color: #fff;
  }

  &__items {
    display: flex;
    margin: 0 0 0 -40px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    margin: 0 0 0 40px;
  }

  &__item-title {
    margin: 0 0 10px;
    font-family: "IBM Plex Mono";
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;

    color: #DABE83;
  }

  &__item-value,
  &__item-link {
    font-family: "IBM Plex Mono";
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;

    color: #fff;
  }

  &__item-link {
    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .5;
    }
  }
}

.r-typography {
  box-sizing: border-box;
  padding: 150px 0 420px;
  background-color: #333333;

  @include media(from_l) {
    padding: 200px 0;
  }

  &__pic {
    width: 327px;
    height: 166px;
    margin: 150px auto 150px;
  }

  &__fonts {
    display: flex;
    justify-content: space-between;
  }

  &__font {
    height: 15px;
    width: 100%;
    max-width: 50%;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: auto 100%;

    @include media(from_l) {
      height: 20px;
      max-width: 50%;
      background-position: right center;
    }
  }

  &__font:nth-child(even) {
    background-position: right center;
  }

  &__font_1 {
    @include media(from_l) {
      max-width: 63%;
    }
  }

  &__font_2 {
    @include media(from_l) {
      max-width: 35%;
    }
  }
}

.r-colors {
  display: flex;

  &__item {
    position: relative;
    width: 25%;
    height: 0;
    padding: 0 0 25%;
  }

  &__item:nth-child(1) {
    background-color: #333333;
  }

  &__item:nth-child(1) &__item-content {
    color: #BFBFBF;
  }

  &__item:nth-child(2) {
    background-color: #292929;
  }

  &__item:nth-child(2) &__item-content {
    color: #BFBFBF;
  }

  &__item:nth-child(3) {
    background-color: #fff;
  }

  &__item:nth-child(3) &__item-content {
    color: #262626;
  }

  &__item:nth-child(4) {
    background-color: #DABE83;
  }

  &__item:nth-child(4) &__item-content {
    color: #262626;
  }

  &__item-content {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-family: "IBM Plex Mono";
    font-size: 18px;
    line-height: 23px;
  }

  &__item-title {
    margin: 0 0 10px;
    font-weight: 600;
  }

  &__item-value {
    font-weight: 500;
  }
}

.r-desk {
  height: 1194px;
  overflow: hidden;
  background-color: #DABE83;

  &__marquee {
    transform: rotate(15deg) translate(-72px, 25px);
    width: 150%;

    @include media(from_l) {
      transform: rotate(15deg) translate(-100px, 178px);
    }
  }

  &__marquee +  &__marquee {
    margin-top: 40px;
  }

  &__pics {
    display: flex;
  }

  &__pic-wrapper {
    margin-right: 40px;
    width: 592px;
    height: 355px;
    overflow: hidden;
    border-radius: 15px;
  }

  &__pic {
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
  }
}

.r-mobile {
  position: relative;
  height: 980px;
  box-sizing: border-box;
  padding: 120px 0;
  background-color: #292929;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 775px;
    height: 737px;
    border-radius: 50%;
    background-color: #DABE83;
  }

  &__pic {
    position: absolute;
    bottom: 53px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 322px;
    height: 643px;
    border-radius: 30px;
  }
}

.r-screen {
  width: 100%;
  height: 100vh;

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.r-pages {

  &__section-1 {
    position: relative;
    height: 680px;
    box-sizing: border-box;
    padding: 100px 0 0;

    background-repeat: no-repeat;
    background-position: right 0 bottom -5px;
    background-size: 382px 276px;
    background-image: url(../../../assets/media/rr-bg-logo.svg);

    @include media(from_l) {
      height: 1016px;
      padding: 150px 0 0;
      background-position: right 0 bottom -188px;
      background-size: 614px 504px;
    }
  }

  &__section-2 {
    height: 1420px;
    padding: 180px 0 460px;
    box-sizing: border-box;
    background-color: #292929;

    @include media(from_l) {
      height: 1670px;
      padding: 277px 0 420px;
    }
  }

  &__container {
    position: relative;
    height: 100%;
  }

  &__title {
    margin: 0 0 80px;

    @include media(from_l) {
      margin: 0 0 123px;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin-left: -226px;

    @include media(from_l) {
      margin-left: -160px;
    }
  }

  &__pic-wrapper {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 677px;
    height: 597px;
    transform: translate(-260px, 119px);
    border-radius: 30px;
    overflow: hidden;
    box-sizing: border-box;
    border: 2px solid transparent;
    background: linear-gradient(101.83deg, #DABE83 -39.95%, #C9792F 162.7%);
    box-shadow: 81.52314758300781px 57.91352081298828px 80px 0px #00000021;

    @include media(from_l) {
      width: 1022px;
      height: 901px;
      bottom: 0;
      transform: translate(-300px, 179px);
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__row-item {
    width: 529px;
    max-width: 529px;
    min-width: 529px;
    height: 715px;
    min-height: 715px;
    max-height: 715px;
    border-radius: 30px;
    overflow: hidden;
    box-shadow: 81.52314758300781px 57.91352081298828px 80px 0px #00000021;

    @include media(from_l) {
      width: 693px;
      min-width: 693px;
      max-width: 693px;
      height: 937px;
      min-height: 937px;
      max-height: 937px;
    }
  }

  &__row-item_pos_left {
    margin-right: 74px;
    transform: translateY(310px);

    @include media(from_l) {
      margin-right: 174px;
      transform: translateY(200px);
    }
  }
}
