@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.card-project {
  position: relative;
  width: 100%;
  height: 100%;

  transition: 1.5s ease-out;
  will-change: transform;
  
  &__pic-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;

    transition: 1.5s ease-out;
    will-change: transform;
  }

  &__pic {
    pointer-events: none;

    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    object-fit: cover;
  }

  &__content {
    position: relative;
    z-index: 1;
    box-sizing: border-box;
    padding: 85px 80px 85px 40px;

    @include media(from_l) {
      padding: 94px 265px 94px 60px;
    }
  }

  &__title {
    margin: 0 0 76px;
    font-family: "Ermilov";
    font-weight: 700;
    font-size: 35px;
    line-height: 110%;
    color: #262626;

    @include media(from_l) {
      font-size: 50px;
    }
  }

  .button-container {
    justify-content: flex-start;
    margin-left: -10px;
  }

  &__button {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 10px 18px 10px 10px;
    background-color: transparent;
    border: none;
    border-radius: 10px;
    color: #262626;
    transition: $transition_main;
  }

  .button-container:active &__button,
  .button-container:hover &__button {
    cursor: pointer;
    background-color: #000;
    color: #fff;
  }

  .button-container:active &__button-icon,
  .button-container:hover &__button-icon {
    background-color: #fff;
  }

  .button-container:active .icon-arrow-right,
  .button-container:hover .icon-arrow-right {
    fill: #262626;
  }

  &__button-text {
    font-family: "IBM Plex Mono";
    font-weight: 600;
    font-size: 16px;
    line-height: 21px;
    color: inherit;
    transition: $transition_main;
  }

  &__button-icon {
    width: 30px;
    height: 30px;
    margin: 0 0 0 10px;
    border-radius: 50%;
    background-color: #D95B4F;
    transition: $transition_main;
  }

  &__button .icon-arrow-right {
    fill: #fff;
  }
}
