@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.card-review {
  width: 100%;
  height: 100%;

  &__inner {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 30px;

    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
  }

  &__header {
    display: flex;
    align-items: flex-start;
    margin: 0 0 20px;
  }

  &__pic-wrapper {
    width: 55px;
    height: 55px;
    margin: 0 10px 0 0;
    border-radius: 50%;
    overflow: hidden;
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: calc(100% - 50px);
  }

  &__name {
    margin: 0 0 5px;
    font-family: "IBM Plex Mono";
    font-weight: 600;
    font-size: 22px;
    line-height: 29px;
    color: #262626;
  }

  &__project {
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #595959;
  }

  &__link {
    position: absolute;
    top: 30px;
    right: 40px;
    display: flex;
    align-items: center;
    color: $red;
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;

    @include media(from_m) {
      right: 30px;
    }
  }

  &__link-text {
    display: none;
  }

  &__desc {
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #595959;

    p {
      margin: 0;
    }
  }
}
