@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.card-project-nav {
  position: relative;
  background-color: #262626;
  overflow: hidden;

  &:hover &__substrate,
  &:active &__substrate {
    transform: scale(1.1);
  }

  
  &:hover &__lines,
  &:active &__lines {
    transform: scale(0.91) translateX(25%);
  }

  &__substrate {
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;
    width: 100%;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    
    transition: $transition_main;
  }

  &__lines {
    display: flex;
    justify-content: center;
    height: 100%;

    transition: $transition_main;

    @include media(from_l) {
      justify-content: space-between;
    }
  }

  &__line {
    width: 1px;
    height: 100%;
    background-color: #fff;
    opacity: .25;

    transition: $transition_main;

    &:nth-child(n + 2) {
      display: none;

      @include media(from_l) {
        display: block;
      }
    }
  }

  &__container {
    position: relative;
    z-index: 1;

    display: flex;
    flex-wrap: wrap;
    align-items: center;

    box-sizing: border-box;
    padding: 110px 0 0;

    @include media(from_l) {
      padding: 0;
      flex-wrap: nowrap;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    flex-basis: 100%;
    max-width: 670px;

    margin: 0 0 40px;

    @include media(from_l) {
      flex-basis: 50%;
      max-width: 50%;
      margin: 0;
    }
  }

  &__title-text {
    font-family: 'Ermilov';
    font-weight: 700;
    font-size: 115px;
    line-height: 80%;

    letter-spacing: 0.01em;

    color: #fff;
  }

  &__title-text_stroked {
    color: transparent!important;
    text-shadow: none;
    -webkit-text-stroke: 2px #fff;
  }

  &__thumb {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50%;
    max-width: 50%;
    height: 200px;
    background-color: #fff;

    @include media(from_l) {
      flex-basis: 25%;
      max-width: 25%;
      order: 1;
      height: 360px;
    }
  }

  &__thumb-pic {
    object-fit: contain;
  }

  &__button-container {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50%;
    max-width: 50%;
    height: 200px;
    padding: 0!important;
    margin: 0!important;

    transition: $transition_main;

    &:active {
      opacity: 0.5;
    }

    @include media(from_l) {
      flex-basis: 25%;
      max-width: 25%;
      height: 360px;
    }
  }

  &__button {
    pointer-events: none;
    width: 100px;
    height: 100px;
    background-color: #fff;
    border-radius: 50%;
    transition: $transition_main;
  }
}


.card-project-nav_type_bmw {

  .card-project-nav {

    &__thumb {
      background-color: #B84236;
    }

    &__button {
      background-color: #D95B4F;
    }
  
    &__title-text_stroked {
      -webkit-text-stroke: 2px #D95B4F;
    }
  }

  .icon-arrow-right-large {
    fill: #fff;
  }
}

