@import "@theme/vars.scss";
@import "@theme-mixins/media.scss";

.c-vacancy-form {

	&__title {
	  font-weight: 700;
	  font-size: 35px;
	  line-height: 110%;
	  color: #262626;
	  margin-bottom: 40px;
	}
  
	&__body {
	  form{
		display: flex;
		flex-wrap: wrap;
		align-items: flex-end;
		align-content: center;
		justify-content: center;
		margin: 0 -10px -30px;
  
		&>*{
		  flex: 0 1 calc(50% - 20px);
		  margin: 0 10px 30px;
		}
	  }
	}
  
	&__body form &__submit {
	  width: auto;
	  margin-top: 10px;
	  margin-right: auto;
	  margin-left: auto;
	  flex-basis: auto;
	  min-width: 225px;
	}

	&__body form &__policy{
		flex-basis: calc(100% - 20px);
		margin-bottom: 20px;
	}

	&__policy{
		font-weight: 400;
		font-size: 14px;
		line-height: 140%;
		color: #595959;
	
		a{
		  color: #F08F3B;
		  transition: color 0.35s ease;
	
		  &:hover{
			color: #D95B4F;
		  }
		}
	  }
  }