@import "@theme/vars.scss";

.button {
  width: 100%;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  font-family: "IBM Plex Mono";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 21px;

  transition: $transition_main;

  &:hover {
    cursor: pointer;
  }

  &__text {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
    transition: $transition_main;
  }

  &__icon {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 0 0 0 15px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    transition: $transition_main;
  }

  &--brackets{
    &:hover{
      background-color: #F08F3B;
      color: #fff;
      border-color: #F08F3B;
    }
  }

  &--brackets &__text{
    margin-right: 10px;
    margin-left: 10px;
  }

  &--brackets:hover &__text{
    margin-left: 15px;
    margin-right: 15px;
  }
}

.button-container {
  background: none;
  padding: 0;
  margin: 0;
  border: none;
}

[data-component="magnetic-button"] [data-entity="button"] {
  will-change: transform;
}

.button_type_main {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  color: #262626;
  background-color: #fff;

  &:active .icon-arrow-right,
  &:hover .icon-arrow-right {
    fill: #fff;
  }

  &:active,
  &:hover {
    background-color: #000;
  }

  &:active .button__text,
  &:hover .button__text {
    color: #fff;
  }

  &:active .button__icon,
  &:hover .button__icon {
    background-color: #fff;
  }

  &:active .icon-arrow-right,
  &:hover .icon-arrow-right {
    fill: #262626;
  }

  .button__icon {
    background-color: #000;
  }

  .icon-arrow-right {
    fill: #fff;
  }  
}

.button-container:hover .button_type_main,
.button-container:active .button_type_main {
  background-color: #000;
}

.button-container:hover .button_type_main .button__icon,
.button-container:active .button_type_main .button__icon {
  background-color: #fff;
}

.button-container:hover .button_type_main .button__text,
.button-container:active .button_type_main .button__text {
  color: #fff;
}

.button-container:hover .button_type_main .icon-arrow-right,
.button-container:active .button_type_main .icon-arrow-right {
  fill: #262626;
}


.button_type_secondary {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 10px;
  color: #fff;
  background-color: #262626;

  &:active .button__icon,
  &:hover .button__icon {
    transform: translateX(8px);
  }

  .button__icon {
    background-color: #fff;
    background-position: center 1px;
    background-image: url("data:image/svg+xml,%3Csvg width='31' height='30' viewBox='0 0 31 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24.1036 14.3536C24.2988 14.1583 24.2988 13.8417 24.1036 13.6464L20.9216 10.4645C20.7263 10.2692 20.4097 10.2692 20.2145 10.4645C20.0192 10.6597 20.0192 10.9763 20.2145 11.1716L23.0429 14L20.2145 16.8284C20.0192 17.0237 20.0192 17.3403 20.2145 17.5355C20.4097 17.7308 20.7263 17.7308 20.9216 17.5355L24.1036 14.3536ZM6.75 14.5H23.75V13.5H6.75V14.5Z' fill='%23262626'/%3E%3C/svg%3E%0A");
  }
}
