@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.card-case {
  width: 100%;
  height: 100%;

  &__inner {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 30px;
    border-radius: 10px;
    background-color: #fff;
  }

  &__title {
    margin: 0 0 10px;
    font-family: "IBM Plex Mono";
    font-weight: 700;
    font-size: 20px;
    line-height: 135%;
    letter-spacing: .01em;
    color: #262626;
  }

  &__pic-wrapper {
    display: flex;
    align-items: flex-end;
    width: 100%;
    height: 46px;
    margin: 0 0 14px;
  }

  &__pic {
    max-height: 100%;
    max-width: 100%;
  }

  &__link {
    display: flex;
    align-items: center;
    font-family: "IBM Plex Mono";
    font-weight: 600;
    font-size: 16px;
    line-height: 145%;
    letter-spacing: .01em;
    color: #D95B4F;

    @include media(from_l) {
      cursor: pointer;
    }
  }

  &__link-text {
    margin: 0 10px 0 0;
  }
}
