@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

[data-component^="slider"] {
  position: relative;
}

[data-entity^="slider-shadow"] {
  position: absolute;
  top: 0;
  z-index: 10;
  pointer-events: none;
  height: 100%;
  width: 170px;
  transition: .2s ease-in-out;
}

[data-entity^="slider-shadow"].hidden {
  opacity: 0;
}

[data-entity="slider-shadow-left"] {
  left: 0;
}

[data-entity="slider-shadow-right"] {
  right: 0;
}
