@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.card-service {
  width: 100%;
  height: 100%;

  &:active &__pic-wrapper,
  &:hover &__pic-wrapper {
    opacity: 1;
  }

  &__inner {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 30px 40px;
    background-color: #fff;
    border-radius: 5px;
    overflow: hidden;

    @include media(from_l) {
      padding: 40px;
      min-height: 360px;
    }
  }

  &__content-wrapper {
    max-width: $cardServiceContentWidth_pad;

    @include media(from_l) {
      display: flex;
      flex-direction: column;
      max-width: $cardServiceContentWidth_desktop;
    }
  }

  &__header {
    display: flex;
    align-items: flex-start;
    padding: 0 0 10px;
  }

  &__icon {
    flex-shrink: 0;
    width: 22px;
    height: 22px;
    margin: 3px 10px 0 0;
    border-radius: 50%;
    background-color: #000;
  }

  &__title {
    margin: 0;
    text-transform: uppercase;
    font-family: "IBM Plex Mono";
    font-weight: 600;
    font-size: 22px;
    line-height: 29px;
    color: #262626;
  }

  &__desc {
    margin: 0 0 14px;
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 21px;

    color: #000000;
  }

  .button-container {
    justify-content: flex-start;
    margin-left: -10px;

    @include media(from_l) {
      margin-top: auto;
    }
  }

  .button {
    border-radius: 10px;
  }

  .button-container:hover .button {
    background-color: #000;
    color: #fff;
  }

  &__pic-wrapper {
    position: absolute;
    bottom: -24px;
    left: calc($cardServiceContentWidth_pad + 108px);
    width: 400px;
    height: 220px;
    border-radius: 10px;
    overflow: hidden;
    opacity: .6;
    transition: $transition_main;
  }

  &__pic {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
