@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.file-loader {
  position: relative;
  transition: 0.3s ease-out;

  &__label {
    position: relative;
  }

  &__input {
    position: absolute;
    opacity: 0;
    pointer-events: none;
  }

  &__input:focus + &__button { 
    outline: 1px solid #c4c4c4;
    border-radius: 2px;
  }

  &__button {
    display: flex;
    align-items: center;
    cursor: pointer;

    outline-offset: 4px;

    transition: $transition_main;

    &:hover,
    &:active {
      opacity: 0.6;
    }
  }

  &__button-title {
    font-family: 'IBM Plex Mono';
    font-weight: 600;
    font-size: 16px;
    line-height: 21px;

    color: #262626;
  }

  &__icon {
    width: 30px;
    height: 30px;
    margin: 0 10px 0 0;
    border-radius: 50%;
    overflow: hidden;
    background-color: #F08F3B;
  }

  &__results {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 24px;
    opacity: 0;
    transform: translateY(-10px);
    transition: $transition_main;
  }

  &__item {
    display: flex;
    align-items: center;
    margin: 0 0 20px;
  }

  &__item-btn {
    width: 24px;
    height: 24px;
    margin-right: 10px;

    padding: 0;
    border: none;

    background: none;
    cursor: pointer;
  }

  &__item-btn:focus {
    outline: 1px solid #c4c4c4;
    border-radius: 2px;
  }

  &__item-btn:hover .icon-cancel,
  &__item-btn:active .icon-cancel {
    stroke: #000;
  }

  &__item-title {
    // width: ;
  }
}

.file-loader.has-items {
  padding-top: 44px;
}

.file-loader.has-items .file-loader__results {
  opacity: 1;
  transform: translateY(0);
}
