@import "@theme/vars.scss";

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2000;
  background: rgba(0,0,0,.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__container {
  width: 100%;
  max-width: 528px;
  box-sizing: border-box;
  padding: 40px 54px;
  overflow-y: auto;
  background-color: #fff;
}

.modal__header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal__title {
  font-family: "Ermilov";
  font-style: normal;
  font-weight: 700;
  font-size: 35px;
  line-height: 140%;
  letter-spacing: .01em;
  text-align: center;
}

.modal__title-text {
  color: #000;
}

.modal__title-text_selected {
  margin-left: 10px;
  color: #F08F3B;
}

.modal__title-text_selected + .modal__title-text {
  margin-left: 10px;
}

.modal__desc {
  margin: 10px 0 0;
  font-family: "Ermilov";
  font-weight: 700;
  font-size: 18px;
  line-height: 145%;
  letter-spacing: .01em;
  text-align: center;
  color: #595959;
}

.modal__desc_slim {
  max-width: 270px;
}

.modal__desc a {
  color: inherit;
  text-decoration: none;
  transition: $transition_main;

  &:active,
  &:hover {
    opacity: .64;
  }
}

.modal__close-wrapper {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin: 0 0 30px;
}

.modal__close {
  width: 24px;
  height: 24px;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.modal__close:active .icon-cancel,
.modal__close:hover .icon-cancel {
  stroke: #000;
}

.modal__header + .modal__content {
  margin-top: 30px;
}


/**************************\
  Demo Animation Style
\**************************/
@keyframes mmfadeIn {

  from { opacity: 0; }

  to { opacity: 1; }
}

@keyframes mmfadeOut {

  from { opacity: 1; }

  to { opacity: 0; }
}

@keyframes mmslideIn {

  from { transform: translateY(15%); }

  to { transform: translateY(0); }
}

@keyframes mmslideOut {

  from { transform: translateY(0); }

  to { transform: translateY(-10%); }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
  z-index: 2000;
}

.micromodal-slide[aria-hidden="false"] .modal__overlay {
  animation: mmfadeIn .5s cubic-bezier(.0, .0, .2, 1);
}

.micromodal-slide[aria-hidden="false"] .modal__container {
  animation: mmslideIn .5s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__overlay {
  animation: mmfadeOut .5s cubic-bezier(.0, .0, .2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__container {
  animation: mmslideOut .5s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
  will-change: transform;
}
