@import "@theme/vars.scss";

.input {
    position: relative;
    box-sizing: border-box;
    background: none;
    border: none;
    outline: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: auto;
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 16px;
    line-height: 135%;
    fill: #262626;
    color: #262626;
    text-decoration: none;
    transition: padding 0.25s ease, max-width 0.25s ease, fill 0.25s ease, color 0.25s ease;

    // @media only screen and (max-width: $bp-lg) {
    //     font-size: 14px;
    // }

    // @media only screen and (max-width: $bp-md) {
    //     font-size: 12px;
    // }

    // @media only screen and (max-width: $bp-sm) {
    //     font-size: 10px;
    // }

    &__field{
        width: 100%;
        height: 38px;
        box-sizing: border-box;
        padding: 0 0 10px;
        border: solid #262626;
        border-width: 0 0 1px;
        background-color: transparent;
        font: inherit;
        outline: none;
        transition: $transition_main;

        &:focus,
        &:hover {
            border-color: #000;
        }

        &::placeholder {
            font: inherit;
            color: #8C8C8C;
        }

        &.has-error {
            border-color: #D95B4F;
        }

        &::placeholder{
            opacity: 1;
            color: #8C8C8C;
            transition: opacity 0.25s ease, color 0.25s ease;
        }

        &[disabled]{
            pointer-events: none;
        }
    }

    &__error{
        box-sizing: border-box;
        display: none;
        position: relative;
        margin-top: 4px;
        z-index: 1;
        color: $red;
        font-size: 14px;
        line-height: 135%;
        width: 100%;
        transition: transform 0.25s ease, opacity 0.25s ease, visibility 0.25s ease, margin 0.25s ease;
    }

    // Textarea
 
    &--textarea{
        max-width: none;
    }

    &--textarea &__field{

    }

    // Actions

    &.has-error &__error{
        display: flex;
        animation: fadeInTop 0.25s ease forwards;
    }

    &.has-error &__field {
        border-color: red;
    }
}