@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.sitemap {

  &__container {
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 64px 20px 80px;

    @include media(from_m) {
      max-width: 834px;
      padding: 104px 40px 144px;
    }

    @include media(from_l) {
      max-width: 1440px;
      padding: 120px 100px 160px;
    }
  }
  
  &__title {
    margin: 0 0 20px;
    font-size: 40px;

    @include media(from_m) {
      margin: 0 0 28px;
      font-size: 56px;
    }

    @include media(from_l) {
      margin: 0 0 32px;
      font-size: 72px;
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__link {
    padding: 12px;
    transform: translateX(-12px);
    font-size: 20px;
    color: #204bd8;

    transition: $transition_main;

    @include media(from_m) {
      padding: 16px;
      transform: translateX(-16px);
      font-size: 24px;
    }

    @include media(from_l) {
      padding: 20px;
      transform: translateX(-20px);
      font-size: 28px;
    }

    &:active,
    &:hover {
      opacity: .4;
      transform: translateX(0);
      cursor: pointer;
    }
  }
}
