@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.error-page {

  &__inner {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    max-height: 100vh;
    overflow: hidden;

    @include media(from_l) {
      flex-direction: row;
    }
  }

  &__content {
    box-sizing: border-box;
    padding: 40px 70px;
    min-height: 50vh;
    max-height: 50vh;

    @include media(from_l) {
      display: flex;
      align-items: center;
      flex-shrink: 0;
      flex-basis: 45%;
      order: -1;
      padding: 0;
      margin-left: calc((100% - $mainContentWidth_desktop) / 2);
      min-height: unset;
      max-height: unset;
    }
  }

  .m-intro {
    display: flex;
    justify-content: space-between;
    padding: 0;
    overflow: visible;
    background-color: transparent;
    
    @include media(from_l) {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }

    &__pic{
      @include media(from_l) {
        margin: 0 20px 0 0;
        width: 162px;
        height: 80px;
        border-radius: 10px;
      }
    }
  }

  .m-intro__title {
    max-width: unset;
  }

  .m-intro__text {
    font-size: 50px;
    color: #262626;

    @include media(from_l) {
      font-size: 85px;
    }

    &_stroked{
      color: transparent;
      -webkit-text-stroke-color: #262626;
    }
  }

  &__link {
    display: inline-block;
    text-decoration: none;
    color: #FFFFFF;

    @include media(from_l) {
      transform: translate(200px);
    }
  }

  &__link-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 162px;
    height: 162px;
    border-radius: 50%;
    background-color: #D95B4F;

    font-family: "Ermilov";
    font-weight: 700;
    font-size: 16px;
    line-height: 14px;
    color: inherit;

    text-decoration: none;
    transition: $transition_main;

    @include media(from_l) {
      width: 210px;
      height: 210px;
    }
  }

  &__art-stage-wrapper { 
    min-height: 50vh;
    max-height: 50vh;
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    // background-position-x: 10%;

    @include media(from_l) {
      flex-basis: 55%;
      min-height: unset;
      max-height: unset;
      // background-position-x: 5%;
    }
  }

  &__art-stage {
    overflow: hidden;
  }

  canvas {
    // display: block;
    // border: none;
    outline: none;
  }
}
