@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.screen-picture {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__pic_desk {
    display: none;

    @include media(from_l) {
      display: block;
    }
  }

  &__pic_pad {
    @include media(from_l) {
      display: none;
    }
  }

  &__logo {
    position: absolute;
    left: 40px;
    bottom: 60px;
    z-index: 1;

    @include media(from_l) {
      left: 100px;
    }
  }
}
