@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    background-color: #32898D;
    background-image: url(../../assets/media/menu-overlay.png);

    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    opacity: 0;
    pointer-events: none;
  }

  &__bar {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 68px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px 40px;

    @include media(from_l) {
      height: 98px;
      padding: 10px 100px;
    }

    .button-container{
      margin-left: auto;
    }
  }

  &__logo {
    opacity: 0;
    width: 56px;
    height: 48px;

    &.is-visible {
      animation: headerTopTransitionOut .5s ease-out forwards;
    }
  }

  &__logo-pic {
    width: 100%;
    height: 100%;
  }

  &__toggler {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    padding: 0;
    background-color: #fff;
    margin-right: -7px;
    border-radius: 50%;
    border: none;
    transition: $transition_main;

    @include media(from_l) {
      width: 78px;
      height: 78px;
      margin-right: -18px;
    }
  }

  &__toggler_revert {
    background-color: #fff;
  }

  .button-container:active &__toggler,
  .button-container:hover &__toggler {
    cursor: pointer;
    background-color: #000;
  }

  .button-container:active &__toggler-line,
  .button-container:active &__toggler-line::after,
  .button-container:active &__toggler-line::before,
  .button-container:hover &__toggler-line,
  .button-container:hover &__toggler-line::after,
  .button-container:hover &__toggler-line::before {
    background-color: #fff;
  }

  &__toggler-lines {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 42px;
    height: 17px;
  }

  &__toggler-line:nth-child(1) {
    background-color: #32898D;
  }

  &__toggler-line:nth-child(2) {
    position: relative;
  }

  &__toggler-line:nth-child(2)::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 8px;
    height: 3px;
    background-color: #F25B41;
    transition: $transition_main;
  }

  &__toggler-line:nth-child(2)::after {
    content: "";
    position: absolute;
    top: 0;
    left: 8px;
    width: 4px;
    height: 3px;
    background-color: #fff;
    transition: $transition_main;
  }

  &__toggler-line {
    width: 100%;
    height: 3px;
    background-color: #595959;

    transition: $transition_main;
  }

  &__toggler_revert &__toggler-line {
    background-color: #595959;
  }

  &__toggler_revert &__toggler-line:nth-child(1) {
    background-color: #32898D;
  }


  &__menu-wrapper {
    display: none;
    height: calc(100vh - 68px);
    min-height: calc(100vh - 68px);
    max-height: calc(100vh - 68px);
    overflow: auto;

    @include media(from_l) {
      height: calc(100vh - 98px);
      min-height: calc(100vh - 98px);
      max-height: calc(100vh - 68px);
    }
  }

  &__menu-box {
    width: 100%;
    box-sizing: border-box;
    padding: 0 40px;
    overflow: hidden;
    
    @include media(from_l) {
      display: flex;
      max-width: $mainContentWidth_desktop;
      padding: 0;
      margin: 0 auto;
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    padding: 66px 0 40px;
    margin-bottom: 40px;
    border: solid rgba($color: #fff, $alpha: .2);
    border-width: 0 0 1px;

    @include media(from_l) {
      flex-shrink: 0;
      padding: 48px 72px 64px 0;
      margin: 0 12px 0 0;
      border-width: 0 1px 0 0;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    margin: 0 0 14px;
    text-decoration: none;

    @include media(from_l) {
      margin: 0 0 25px;
      cursor: none;
    }
  }

  &__link:active &__link-text {
    color: $yellow;

    @include media(from_l) {
      color: #fff;
    }
  }
  
  &__link:hover &__link-text {
    @include media(from_l) {
      color: #fff;
    }
  }

  &__link:last-child {
    margin: 0;
  }

  &__link-pic {
    display: block;
    width: 80px;
    height: 80px;
    margin: 0 30px 0 0;
    border-radius: 50%;
    box-sizing: border-box;
    border: 3px solid $yellow;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    pointer-events: none;

    @include media(from_l) {
      display: none;
    }
  }

  &__link-text {
    font-family: "Ermilov", sans-serif;
    font-weight: 700;
    font-size: 55px;
    line-height: 110%;

    letter-spacing: .01em;
    color: #fff;
    text-transform: uppercase;

    pointer-events: none;

    transition: $transition_main;

    @include media(from_l) {
      font-size: 66px;
      color: transparent;
      text-shadow: none;
      -webkit-text-stroke: 1px #F0F0F0;
    }
  }

  &__map-box {
    display: flex;
    align-items: center;

    @include media(from_l) {
      align-items: flex-end;
      padding-bottom: 94px;
    }
  }

  &__map {
    flex-shrink: 0;
    width: 357px;
    height: 357px;
    border-radius: 50%;
    background-color: #fff;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;

    @include media(from_l) {
      width: 421px;
      height: 421px;
    }
  }

  &__map-capture {
    width: 285px;
    font-family: "Ermilov", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    text-transform: uppercase;
    line-height: 135%;
    letter-spacing: .01em;
    color: $yellow;

    transform: translateX(-18px);

    @include media(from_l) {
      transform: translate(-104px, -16px);
    }
  }
}

.header_has_bg {
  background-color: #fff;
}

.header_type_full {
  height: 100vh;
  max-height: 100vh;
  z-index: 1000;
  cursor: none;

  &::before {
    opacity: 1;
    transition: .2s ease-in;
  }

  .header {

    &__logo {
      opacity: 1;
      animation: headerTopTransitionIn .5s ease-out;
      
      @include media(from_l) {
        cursor: none;
      }
    }

    &__toggler {
      background-color: transparent;
    }

    &__toggler_revert {
      background-color: transparent;
    }

    &__toggler-line:nth-child(1) {
      background-color: #fff;
      transform: rotate(45deg) translate(8px, 8px);
    }
  
    &__toggler-line:nth-child(2) {
      opacity: 0;
    }

    &__toggler-line:nth-child(3) {
      background-color: #fff;
      transform: rotate(-45deg) translate(1px, -2px);
    }

    &__menu-wrapper {
      position: relative;
      z-index: 1;
      display: block;
      animation: fadeIn .5s ease-out;
    }  

    &__nav {
      animation: headerBottomTransitionIn .5s ease-out;
    }

    &__map-box {
      animation: headerBottomTransitionIn .5s ease-out;
    }
  }

  .button-container {
    animation: headerTopTransitionIn .5s ease-out;

    @include media(from_l) {
      cursor: none!important;
    }
  }

  .button-container:active .header__toggler,
  .button-container:hover .header__toggler {
    background-color: transparent;
  }

  .button-container:active .header__toggler-line,
  .button-container:hover .header__toggler-line {
    background-color: $yellow;
  }
}

.header_type_compact {

  .header {

    &__inner::before {
      animation: headerMenuOverlayTransitionOut .5 ease-out;
      animation-fill-mode: forwards;
    }

    &__logo.is-visible {
      animation: headerTopTransitionOut .5s ease-out forwards;
    }

    &__menu-wrapper {
      display: none;
      animation: fadeOut .4s ease-out;
    }

    &__nav {
      animation: headerBottomTransitionOut .5s ease-out;
    }

    &__map-box {
      animation: headerBottomTransitionOut .5s ease-out;
    }
  }

  .button-container {
    animation: headerTopTransitionOut .5s ease-out;
  }
}

.header {

  .logo-path-letter {
    transition: $transition_main;
  }
}

.header_type_full {

  .logo-path-center-outer {
    fill: #F0F0F0;
  }

  .logo-path-center-inner {
    fill: #D9D9D9;
  }

  .logo-path-letter {
    fill: #32888F;
  }

  .logo-path-triangle-1 {
    fill: #F0F0F0;
  }

  .logo-path-triangle-2 {
    fill: #D9D9D9;
  }

  .logo-path-triangle-3 {
    fill: #D9D9D9;
  }

  .logo-path-triangle-4 {
    fill: #F0F0F0;
  }

  .logo-path-triangle-5 {
    fill: #FFF;
  }

  .logo-path-triangle-6 {
    fill: #D9D9D9;
  }
}
