@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.screen-intro {
  position: relative;
  width: 100%;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;
  background-color: #262626;

  &__bg {
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;
    width: 100%;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: #262626;
  }

  &__bg-lines {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0 40px;

    @include media(from_l) {
      padding: 0 100px;
      justify-content: space-between;
    }
  }

  &__bg-line {
    width: 1px;
    height: 100%;
    background-color: #fff;
    opacity: .25;

    &:nth-child(n + 2) {
      display: none;

      @include media(from_l) {
        display: block;
      }
    }
  }

  &__content {
    position: relative;
    z-index: 1;

    display: flex;
    flex-direction: column;
    align-items: flex-start;

    height: 100%;
    width: 100%;

    box-sizing: border-box;
    padding: 320px 40px 0 40px;
    margin: 0 auto;

    @include media(from_l) {
      padding: 272px 100px 0;
      align-items: stretch;
    }
  }

  &__header {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    margin-left: auto;
    
    @include media(from_l) {
      flex-direction: row;
      justify-content: space-between;
      padding: 0 150px 0 0;
      margin-left: 0;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 0 0 22px;
    font-family: "Ermilov";
    font-style: normal;
    font-weight: 700;
    font-size: 115px;
    line-height: 80%;
    letter-spacing: .01em;
    text-transform: uppercase;
    color: #fff;

    @include media(from_l) {
      order: 1;
      margin: 0;
    }
  }

  &__title-part {
    text-align: right;
  }

  &__title-part_type_stroke {
    color: transparent;
    text-shadow: none;
    -webkit-text-stroke: 1px #fff;
  }

  &__info {
    display: flex;
    margin: -40px 0 0 -40px;
  }

  &__info-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 40px 0 0 40px;

    font-family: "IBM Plex Mono";
    color: #fff;
  }

  &__info-title {
    margin: 0 0 10px;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
  }

  &__info-value {
    font-size: 16px;
    line-height: 21px;
  }

  &__thumb {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 80%;
    margin-top: auto;
    margin-left: 0;
    background-color: #fff;

    @include media(from_l) {
      width: 25%;
      height: 100%;
    }
  }

  &__thumb-pic {
    object-fit: contain;
  }

  &__button-container {
    position: absolute!important;
    bottom: 126px;
    right: 48px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 160px;
    height: 160px;

    @include media(from_l) {
      bottom: 100px;
      right: 25%;
      transform: translateX(20%);
    }

    @media (min-width: 1240px) and (max-height: 700px) {
      bottom: 5px;
    }
  }

  &__button {
    pointer-events: none;
    width: 100px;
    height: 100px;
    background-color: #fff;
    border-radius: 50%;
    background-size: 100px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M60.5102 68.0604L60.8295 56.5178L50.6737 62.0125L60.5102 68.0604ZM40.1205 32.4759L55.348 60.6206L57.107 59.6688L41.8795 31.5241L40.1205 32.4759Z' fill='%23262626'/%3E%3C/svg%3E%0A");
  }
}
