@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.footer {
  background-color: #1F1F1F;
  color: #fff;

  &__main {
    width: 100%;
    box-sizing: border-box;
    padding: 150px 40px 100px;
    margin: 0 0 50px;
    border-bottom: 1px solid rgba($color: #fff, $alpha: .15);

    @include media(from_l) {
      padding: 90px;
    }
  }

  &__container {
    width: 100%;
    max-width: $mainContentWidth_desktop;
    margin: 0 auto;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    margin: 0 0 0 -40px;
  }

  &__col {
    flex-shrink: 0;
    max-width: 50%;
    margin: 0 0 0 40px;
  }

  &__title {
    margin: 0 0 40px;
    font-family: "Ermilov";
    font-weight: 700;
    font-size: 35px;
    line-height: 110%;
    color: #fff;

    @include media(from_l) {
      font-size: 50px;
    }
  }

  &__contacts {
    padding-top: 40px;
    margin-bottom: 30px;

    @include media(from_l) {
      padding-top: 0;
      margin-bottom: 100px;
    }
  }

  &__contacts a {
    transition: $transition_main;
  }

  &__contacts a:active,
  &__contacts a:hover {
    color: #fff;
  }

  &__contacts-list {
    display: flex;
    flex-wrap: wrap;
    margin: -30px 0 0 -134px;

    @include media(from_l) {
      margin: -30px 0 0;
    }
  }

  &__contacts-item {
    margin: 30px 0 0 134px;

    @include media(from_l) {
      flex-basis: 100%;
      margin: 30px 0 0;
    }
  }

  &__contacts-item_order_first-desk {
    @include media(from_l) {
      order: -1;
    }
  }

  &__contacts-title {
    margin: 0 0 10px;
    font-family: "IBM Plex Mono";
    font-weight: 700;
    font-size: 18px;
    line-height: 145%;
    letter-spacing: .01em;
    color: #fff;
  }

  &__contacts-values {
    display: flex;
    flex-direction: column;
    margin: -5px 0 0;
  }

  &__contacts-value {
    display: flex;
    align-items: center;
    margin: 5px 0 0;
  }

  &__contacts-text {
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 18px;
    line-height: 145%;
    letter-spacing: .01em;
    text-decoration: none;
    color: #BFBFBF;
  }

  &__contacts-icon {
    flex-shrink: 0;
    width: 30px;
    height: 30px;
    margin: 0 0 0 10px;
    border-radius: 50%;
    background-color: #fff;
  }

  &__contacts-icon_type_location {
    background-color: #D95B4F;
  }

  &__bottom {
    width: 100%;
    box-sizing: border-box;
    padding: 0 40px 50px;

    @include media(from_l) {
      padding: 0 90px 50px;
    }
  }

  &__bottom-row {
    display: flex;
    justify-content: space-between;
  }

  &__rights {
    display: flex;
    align-items: center;
  }

  &__rights-text {
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 15px;
    line-height: 145%;
  }

  &__rights-icon {
    width: 16px;
    height: 16px;
    margin: 0 5px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 16px;
    background-image: url("data:image/svg+xml,%3Csvg width='17' height='16' viewBox='0 0 17 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.5 8C1.5 9.85652 2.2375 11.637 3.55025 12.9497C4.86301 14.2625 6.64348 15 8.5 15C10.3565 15 12.137 14.2625 13.4497 12.9497C14.7625 11.637 15.5 9.85652 15.5 8C15.5 6.14348 14.7625 4.36301 13.4497 3.05025C12.137 1.7375 10.3565 1 8.5 1C6.64348 1 4.86301 1.7375 3.55025 3.05025C2.2375 4.36301 1.5 6.14348 1.5 8ZM16.5 8C16.5 10.1217 15.6571 12.1566 14.1569 13.6569C12.6566 15.1571 10.6217 16 8.5 16C6.37827 16 4.34344 15.1571 2.84315 13.6569C1.34285 12.1566 0.5 10.1217 0.5 8C0.5 5.87827 1.34285 3.84344 2.84315 2.34315C4.34344 0.842855 6.37827 0 8.5 0C10.6217 0 12.6566 0.842855 14.1569 2.34315C15.6571 3.84344 16.5 5.87827 16.5 8ZM8.646 4.992C7.434 4.992 6.719 5.912 6.719 7.494V8.554C6.719 10.125 7.422 11.016 8.646 11.016C9.625 11.016 10.287 10.43 10.375 9.598H11.67V9.691C11.57 11.139 10.316 12.158 8.64 12.158C6.549 12.158 5.371 10.822 5.371 8.555V7.482C5.371 5.221 6.572 3.844 8.641 3.844C10.322 3.844 11.576 4.898 11.67 6.416V6.504H10.375C10.287 5.625 9.607 4.992 8.646 4.992Z' fill='white'/%3E%3C/svg%3E%0A");
  }

  &__nav {
    display: flex;
    align-items: center;
    margin-left: -15px;
  }

  &__link {
    margin-left: 15px;
    text-decoration: none;
    color: inherit;
    font-family: "IBM Plex Mono";
    font-weight: 500;
    font-size: 16px;
    line-height: 145%;
    letter-spacing: .01em;
    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .8;
    }
  }

  &__payment-icons {
    max-width: 390px;
    max-height: 39px;
  }
}

.footer {
  .form {

    max-width: 300px;

    @include media(from_l) {
      max-width: 360px;
    }

    &__group {
      flex-basis: 100%;
    }

    &__group-row {
      margin: -20px 0 0;

      @include media(from_l) {
        justify-content: space-between;
        align-items: flex-end;
      }
    }

    &__group-item {
      margin: 20px 0 0;

      @include media(from_l) {
        flex-basis: unset;
      }
    }

    &__additional-title,
    .file-loader__button-title  {
      color: #fff;
    }

    &__additional-icon,
    .file-loader__icon {
      background-color: #F9F9F9;
    }

    .icon-add-file,
    .icon-download-file {
      fill: #000;
    }

    .icon-cancel {
      stroke: #F9F9F9;
    }

    .file-loader__results {
      @include media(from_l) {
        min-width: 360px;
      }
    }
  }
}
