@import "@theme/vars.scss";

@import "@theme-mixins/media.scss";

.gtm-screen-intro {

  .screen-intro {

    &__header {
      margin-bottom: 76px;

      @include media(from_l) {
        position: relative;
        flex-direction: column;
        align-items: start;
        max-width: unset;
        padding: 0;
        margin: 0;
        transform: translateY(200px);
      }
    }

    &__title {
      transform: translateX(-60px);

      @include media(from_l) {
        order: unset;
        margin: 0 0 76px 25%;
        transform: translateX(0);
      }
    }

    &__title-part_type_stroke {
      transform: translateX(60px);
    }

    &__info {
      padding-left: 100px;

      @include media(from_l) {
        flex-direction: column;
        padding-left: calc(50% + 30px);
      }
    }

    &__thumb {
      background-color: #595959;

      @include media(from_l) {
        margin-left: 25%;
      }
    }

    &__button-container {
      @include media(from_l) {
        transform: translateX(-15%);
      }
    }

    &__button {
      background-color: #FF7B31;
      background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M60.5102 68.0604L60.8295 56.5178L50.6737 62.0125L60.5102 68.0604ZM40.1205 32.4759L55.348 60.6206L57.107 59.6688L41.8795 31.5241L40.1205 32.4759Z' fill='white'/%3E%3C/svg%3E%0A");
    }
  }
}

.gtm-progress {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "IBM Plex Mono";
  font-weight: 400;
  font-size: 18px;
  line-height: 151.69%;
  color: #262626;

  &__count,
  &__title,
  &__subtitle {
    margin: 0;
    font-family: inherit;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
  }
}

.gtm-progress_theme_light {
  color: #BFBFBF;
}


.gtm-info {
  background-color: #2F2F2F;

  &__inner {
    height: 100vh;
    box-sizing: border-box;
    padding: 130px 0 0;
    background-size: cover;
    background-position-x: 35%;

    @include media(from_l) {
      padding: 120px 0 0;
    }
  }

  &__logo {
    margin-bottom: 20px;
  }

  &__desc {
    max-width: 358px;
    margin: 0 0 30px;
    font-family: "IBM Plex Mono";
    font-weight: 400;
    font-size: 18px;
    line-height: 151.69%;

    color: #BFBFBF;
  }

  &__items {
    display: flex;
    margin: 0 0 0 -40px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 0 40px;
    font-family: "IBM Plex Mono";
    font-weight: 500;
    font-size: 18px;
    line-height: 23px;
    color: #BFBFBF;
  }

  &__item-title {
    margin: 0 0 10px;
    font-weight: 700;
    color: #F08F3B;
  }

  &__item-link {
    color: inherit;
    transition: $transition_main;

    &:active,
    &:hover {
      opacity: .5;
    }
  }
}


.gtm-desk {

  &__inner {
    padding: 100px 0 0;
    background-color: #262626;

    @include media(from_l) {
      padding: 120px 0 0;
    }
  }

  &__header {
    margin: 0 0 60px;

    @include media(from_l) {
      margin: 0 0 100px;
    }
  }

  &__pic-wrapper {
    width: 100%;
    max-width: 657px;
    height: 1535px;
    margin: 0 auto;
    border-radius: 30px;
    overflow: hidden;
    margin-top: -118px;
    transform: translateY(118px);

    @include media(from_l) {
      max-width: 876px;
      height: 2045px;
      margin-top: -146px;
      transform: translateY(146px);
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.gtm-typography {

  &__inner {
    padding: 250px 0 100px;
    background-color: #fff;

    @include media(from_l) {
      padding: 250px 0 156px;
    }
  }

  &__header {
    margin: 0 0 100px;

    @include media(from_l) {
      margin: 0 0 120px;
    }
  }

  &__items {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1020px;
    margin: 0 auto;
  }

  &__item {
    display: flex;
    flex-direction: column;
  }

  &__item_type_color {
    width: 100%;
    max-width: 170px;

    @include media(from_l) {
      max-width: 195px;
    }
  }

  &__item_type_color + &__item_type_color {
    transform: translateX(-14px);

    @include media(from_l) {
      transform: translateX(-24px);
    }
  }

  &__item_type_font {
    width: 100%;
    max-width: 350px;
  }

  &__item-pic {
    width: 100%;
    height: 206px;

    margin: 0 0 20px;

    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

    @include media(from_l) {
      height: 260px;
      margin: 0 0 25px;
    }
  }

  &__item-value {
    font-family: "IBM Plex Mono";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 151.69%;
    text-align: center;

    color: #262626;
  }

  &__item-values {
    display: flex;
    justify-content: space-between;
  }
}

.gtm-mob {

  &__inner {
    background-color: #fff;
  }

  &__header {
    margin: 0 0 35px;

    @include media(from_l) {
      margin: 0 0 60px;
    }
  }

  &__row {
    display: flex;
    padding: 0 0 50px;
    background-color: #121212;
    background-repeat: no-repeat;
    background-size: 950px 518px;
    background-position: right calc(((100%  - $mainContentWidth_pad) / 2 ) + 190px) bottom 0;
    background-image: url(../../../assets/media/gtm-pic-mob-bg.svg);

    @include media(from_l) {
      padding: 0 0 15px;

      background-size: 1385px 525px;
      background-position: right calc(((100%  - $mainContentWidth_desktop) / 2 ) + 320px) bottom 0;
    }
  }

  &__col {
    flex-basis: 50%;
    height: 970px;

    @include media(from_l) {
      height: 1088px;
    }
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  &__item_height_half {
    height: 50%;
  }

  &__item_type_main {
    background-color: #FF7B31;
  }

  &__item_type_secondary {
    background-color: #fff;
  }

  &__pic-wrapper {
    width: 220px;
    height: 420px;
    box-sizing: border-box;
    border: 3px solid #FD7A31;
    border-radius: 30px;
    overflow: hidden;
    box-shadow: 48px 34px 42px 0px #00000021;

    @include media(from_l) {
      width: 229px;
      height: 437px;
    }
  }

  &__item_type_main &__pic-wrapper {
    width: 267px;
    height: 510px;
    border-color: #B3B3B3;

    @include media(from_l) {
      width: 353px;
      height: 671px;
    }
  }

  &__item_type_secondary &__pic-wrapper {
    border-color: #B3B3B3;
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.gtm-pages {

  &__inner {
    padding: 137px 0 0;
    background-color: #121212;

    @include media(from_l) {
      padding: 122px 0 0;
    }
  }

  &__header {
    margin: 0 0 22px;

    @include media(from_l) {
      margin: 0 0 103px;
    }
  }

  &__fullscreen {
    height: 526px;
    margin-top: -125px;
    transform: translateY(125px);

    @include media(from_l) {
      height: 815px;
      margin-top: -145px;
      transform: translateY(145px);
    }
  }

  &__fullscreen-inner {
    @include media(from_l) {
      width: 100%;
      box-sizing: border-box;
      padding: 0 50px;
    }
  }

  &__pic-wrapper {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border: 3px solid #636363;
    border-radius: 20px;
    overflow: hidden;
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__row-wrapper {
    padding: 220px 0 100px;
    background-color: #262626;

    @include media(from_l) {
      padding: 296px 0 176px;
    }
  }

  &__row {
    width: 100%;
    display: flex;
    align-items: flex-start;
    margin: 0 -40px;
    transform: translateX(-395px);

    @include media(from_l) {
      box-sizing: border-box;
      margin: 0;
      padding-right: 40px;
      transform: translateX(-185px);
    }
  }

  &__col-left {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    flex-basis: 100%;
    margin: 0 108px 0 0;
  }

  &__col-right {
    flex-basis: 354px;
    flex-shrink: 0;

    @include media(from_l) {
      flex-basis: 408px;
    }
  }

  &__col-left &__col-item:nth-child(1) {
    width: 755px;
    height: 812px;

    @include media(from_l) {
      width: 870px;
      min-width: 870px;
      height: 936px;
    }
  }

  &__col-left &__col-item:nth-child(2) {
    width: 526px;
    height: 785px;

    @include media(from_l) {
      width: 606px;
      height: 905px;
    }
  }

  &__col-left &__col-item:nth-child(3) {
    width: 667px;
    height: 549px;

    @include media(from_l) {
      width: 768px;
      height: 632px;
    }
  }

  &__col-right &__col-item {
    height: 2331px;

    @include media(from_l) {
      height: 2687px;
    }
  }

  &__col-item + &__col-item {
    margin-top: 100px;
  }
}
